/**
 * @fileoverview This is used to create a PlusButton component.
 * @class
 * @augments Component
 * @author LIVELOGIC
 * @version 1.0
 */
function ComponentPlusButton(){
}

ComponentPlusButton.prototype = Object.create(Component.prototype);

/**
 * @function
 * @description  This function initialiases the component
 */
ComponentPlusButton.prototype.init = function(_componentController, node, _id, _num){
    Component.prototype.init.call(this, _componentController, node, _id, _num);

    var self = this;

    // this.managesCompletion = true;

    //self.managesCompletion = false;
}

/**
 * @function
 * @description  This function adds the component HTML
 */
ComponentPlusButton.prototype.getHTML = function(){
    var self = this;    
    var htmlStr = '';
    var path = '';
    var altText = (this.dataNode.alt) ? this.dataNode.alt[0].Text : '';

    htmlStr += '<div class="plusbutton-container">'
        if(this.dataNode.src){
            path = utils.getImagePath(this.dataNode.src);
            htmlStr += '<img src="'+path+'" class="plusbutton-img" alt="'+altText+'"/>';
        }

        if (this.dataNode.text){
            if(this.dataNode.displayHeader == 0){
                htmlStr += `<div class="plusbutton-text sr-only">
                    ${this.dataNode.text[0].Text}
                </div>`;
            }
            else{
                htmlStr += 
                    `<div class="plusbutton-text">
                        ${this.dataNode.text[0].Text}
                    </div>`;
            }
        }

        htmlStr += `
            <div class="plus-btn">
                <div class="plus-icon-container">
                    <div class="plus-btn-icon-plus">
                        ${svgUtils.createSVG('plus')}
                    </div>
                    <div class="plus-btn-icon-tick">
                        <span class="sr-only">${courseData.getAccessibleLabel('complete')}</span>
                        ${svgUtils.createSVG('tick')}
                    </div>
                </div>
            </div>
        `;
    htmlStr += '</div>'
    return htmlStr;
}

/**
 * @function
 * @description  This function sets up the component
 */
ComponentPlusButton.prototype.setupComponent = function(){
    Component.prototype.setupComponent.call(this);
    
    var self = this;  
    
    self.$component
        .attr('role', 'button')
        .attr('tabindex', '0');
        
    // self.$component.on('click', function(){
    //     $(self.$component.find(".plus-icon-container")[0]).html(svgUtils.createSVG("tick"));
    //     $(self.$component.find(".plus-btn")[0]).css("background-color", "#888888")
    // });
}

/**
 * @function
 * @description  This function fires when sets up complete
 */
ComponentPlusButton.prototype.setupComplete = function(){
    Component.prototype.setupComplete.call(this);

    var self = this;   
}

/**
 * @public
 * @override 
 * @description  This function is called on unload
 */
ComponentPlusButton.prototype.unload = function(){
    var self = this;    
}

/**
 * @public
 * @override 
 * @description  This function fired when component is displayed
 */
ComponentPlusButton.prototype.componentDisplayed = function(){
    Component.prototype.componentDisplayed.call(this);

    var self = this;    
    
    // set component complete
    //self.componentController.setComponentComplete(self);
}
/**
 * @public
 * @override 
 * @description  This function is called on browser resize
 */
ComponentPlusButton.prototype.resize = function(){
    var self = this;
    
    var $plusButtonComponents = self.$component.closest('.slide').find('.component-plusbutton').filter('.label-bottom, .plusbutton-icon');
    
    // is this the first component of this type on this slide?
    if (self.$component.attr('id') == $plusButtonComponents.first().attr('id')){
        var $plusButtonTexts = $plusButtonComponents.find('.plusbutton-text');

        // resize when we're using columns so heights match
        // console.log('resize ')
        utils.matchElementHeights($plusButtonTexts, ['xs']);
    }
}

/**
 * @public
 * @override 
 * @description  This function is called on device breakpoint changed
 */
 ComponentPlusButton.prototype.breakpointChanged = function(){
    var self = this;
 }
