 /**
 * @fileoverview This is used to create an ComponentDropdownsequence component.
 * @class
 * @author LIVELOGIC
 * @version 1.0
 */
function ComponentDropdownsequence(){
}

ComponentDropdownsequence.prototype = Object.create(Component.prototype);

/**
 * @function
 * @description  This function adds the component HTML
 */
ComponentDropdownsequence.prototype.init = function(_componentController, node, _id, _num){
    Component.prototype.init.call(this, _componentController, node, _id, _num);

    var self = this;
    this.managesCompletion = true;

    this.answerArray = [];
    // will be passed to the assessmentController
    this.userSelectionArray = [];
    this.selectOptionsArray = [];
}

/**
 * @function 
 * @description  This function adds the component HTML
 */
ComponentDropdownsequence.prototype.getHTML = function(){
    var self = this;
    var htmlStr = '';

    var submit = this.dataNode.submit[0].text[0].Text;

    htmlStr += '<div class="questionPanel">';
        if (this.dataNode.assessment){
            htmlStr += '<div class="questionNum moduleColor">'+(self.questionNum+1)+'</div>';
        }
        htmlStr += '<div class="questionInfo">';
            htmlStr += '<div class="question-text">';
                htmlStr += htmlUtils.getHTMLText(this.dataNode.question[0]);
            htmlStr += '</div>';
        htmlStr += '</div>';
    htmlStr += '</div>';
    if (self.dataNode.instruction){
        htmlStr += self.componentController.generateComponents(self.dataNode.instruction[0]);
    }
    var options = this.dataNode.options[0].option;
    self.selectOptionsArray = [];
    for(let o = 0; o < options.length; o++){
        self.selectOptionsArray.push(o);
    }
    self.selectOptionsArray.shuffle();
    for(let i = 0; i < options.length; i++){
        // store the 
        self.answerArray.push(i);

        htmlStr += `<div class="mb-4">`;
            htmlStr += `<div class="label-select-group">`;
                htmlStr += `<div class="ds-label" >
                                <label id="${self.id}-label-option-${i}" for="${self.id}-option-${i}">${self.dataNode.options[0].option[i].label[0].Text}</label>
                            </div>`;

                htmlStr += '<div class="dropdown-clear-btn-group">';
                    htmlStr += '<div class="option" data-index="'+i+'">';
                        htmlStr += '<select id="'+self.id+'-option-'+i+'" data-index="'+i+'">';
                            htmlStr += '<option selected>';
                                htmlStr += (self.dataNode.selectblank ? self.dataNode.selectblank[0].Text : '');
                            htmlStr += '</option>';
                            for (let k = 0; k < options.length; k++){
                                var num = self.selectOptionsArray[k];
                                htmlStr += '<option value="'+(num+1)+'">';
                                    htmlStr += self.dataNode.options[0].option[num].match[0].Text;
                                htmlStr += '</option>';  
                            }
                        htmlStr += '</select>';
                        htmlStr += '<div class="tickCross" aria-hidden="true">'+svgUtils.createSVG('tickcross')+'</div>';
                    htmlStr += '</div>';
                    htmlStr += `<button class="ds-clear-btn btn disabled" aria-disabled="true" data-ds-clear-btn="${i}">${self.dataNode.cancelBtnText[0].Text}</button>`
                htmlStr += '</div>';
            htmlStr += '</div>';
        htmlStr += '</div>';
    }
    htmlStr += '<button class="btn ok pull-right disabled anim fadeInUp mb-3" aria-disabled="true">'+submit+'</button>';
    
    if (self.isAssessmentQuestion){
        console.log("self.answerArray initQuestion", self.answerArray);
        self.assessmentController.initQuestion(self.questionNum, self.answerArray);
    }

    return htmlStr;
} 

/**
 * @function
 * @description  This function sets up the component
 */
ComponentDropdownsequence.prototype.setupComponent = function(){
    Component.prototype.setupComponent.call(this);
    
    var self = this;   
}

/**
 * @function
 * @description  This function fires when sets up complete
 */
ComponentDropdownsequence.prototype.setupComplete = function(){
    Component.prototype.setupComplete.call(this);
    
    var self = this;   

    self.$feedback = self.$component.closest('.slide').find('.feedback');
    feedbackController.initFeedback(self.$component, self.$feedback);

    self.checkDone();

    self.$component.find('.btn.ok').on('click keypress', utils.handleClickAndPress(function (e) {
        var $this = $(e.currentTarget);

        var isDone = false;
        var isCorrect = false;

        if ($this.hasClass('disabled')){
            return false;
        }

        $this.hide();

        var $options = self.$component.find('.option');
        $options.each(function(){
            var $this = $(this);
            var dropIndex = $(this).attr('data-index');
            $(this).addClass('selected disabled');
            $(this).find('select').selectmenu('disable');

            if (self.userSelectionArray[dropIndex] == self.answerArray[dropIndex]){
                $this.closest('.label-select-group').find('.option').addClass('correct');
            } else {
                $this.closest('.label-select-group').find('.option').addClass('incorrect');
            }
        });

        self.$component.find('.ds-clear-btn').disable();

        self.numCorrectSelected = 0;
        self.numIncorrectSelected = 0;

        var userAnsweredCorrectly = function(correctArray, userSelectionArray){
            if(correctArray.length !== userSelectionArray.length){
                return false;
            }

            for(let i = 0; i < correctArray.length; i++){
                if(correctArray[i] !== userSelectionArray[i]){
                    return false;
                }
            }
            return true;
        }

        var userAnsweredPartlyCorrectly = function(correctArray, userSelectionArray){
            if(correctArray.length !== userSelectionArray.length){
                return false;
            }

            var numCorrect = 0;
            for(let i = 0; i < correctArray.length; i++){
                if(correctArray[i] === userSelectionArray[i]){
                    numCorrect++;
                }
            }
            return numCorrect > 0 && numCorrect < correctArray.length;
        }

        self.$feedback.find('.feedbackDone').show();
        if(userAnsweredCorrectly(self.answerArray, self.userSelectionArray)){
            isCorrect = true;
            isDone = true;
            self.$feedback.removeClass('incorrect partlycorrect').addClass('correct');
        } else {
            if (self.$feedback.find('.feedbackPartlyCorrect').length && userAnsweredPartlyCorrectly(self.answerArray, self.userSelectionArray)){
                self.$feedback.removeClass('incorrect').addClass('partlycorrect');
                isDone = true;
            } else {
                isDone = true;
                self.$feedback.removeClass('partlycorrect').addClass('incorrect');
            }
        }
        feedbackController.showFeedback(self.$component, self.$feedback);
        self.$component.find('.component-instruction').slideUp();

        if (self.isAssessmentQuestion){
            self.assessmentController.storeAnswers(self.questionNum, self.userSelectionArray);//, self.isCorrectArray);
            htmlUtils.updateTextVariables();
        } 

        // let's save the interaction
        storageController.setInteraction({
            id: self.interactionID, 
            type: 'choice', 
            question: self.$component.find('.questionInfo').text(),
            response: self.userSelectionArray, 
            answers: self.answerArray, 
            correct: isCorrect,
            timestamp: self.timeDisplayed
        });

        // automatically store on custom data questions if not set - not for assessments
        if (!self.isAssessmentQuestion){
            // store answer in suspend data
            if (self.customDataObj == undefined){
                self.customDataObj = {
                    answers: self.answerArray,
                    attempts: [],
                    correct: 0
                }
            }
            if (self.customDataObj.correct == 0){
                console.log('attempts', self.customDataObj.attempts)
                self.customDataObj.attempts.push(self.userSelectionArray.slice());
                self.customDataObj.correct = utils.booleanToInt(isCorrect);
                customDataController.setCustomData(self.customData, self.customDataObj);
            }
        }

        if (isDone){
            self.$component.addClass('done');
            
            $options.each(function(){
                var $this = $(this);
                var optionIndex = $(this).attr('data-index');
                correctLabel = courseData.getAccessibleLabel(self.userSelectionArray[optionIndex] === self.answerArray[optionIndex] ? 'correct' : 'incorrect');
                $this.find('.ui-selectmenu-text').prepend('<span class="sr-only">'+correctLabel+'&nbsp;</span>');
            });
            self.componentController.setComponentComplete(self);
        } else {

        }
    
    }));
}

/**
 * @public
 * @override 
 * @description  This function is called on unload
 */
ComponentDropdownsequence.prototype.unload = function(){
    var self = this;    
}

/**
 * @public
 * @override 
 * @description  This function fired when component is displayed
 */
ComponentDropdownsequence.prototype.componentDisplayed = function(){
    Component.prototype.componentDisplayed.call(this);
    var self = this;
    self.$component.find('select').selectmenu({
        create: function(event, ui){
            var $this = $(this);
            setLabelledBy($this);    
            // add focus event on button to set aria-labelledby attribute 
            $this.selectmenu("widget").on('focus', function(){
                setLabelledBy($this); 
            });
        },
        focus: function(event, ui){
            // note this is not the same as the focus event on the selectmenu widget
            setLabelledBy($(this));
        },
        change: function(event, ui){
            var labelIdx = $(this).closest('.option').data('index');
            var selectedValue = ui.item.value;
            if(!isNaN(self.userSelectionArray[labelIdx]) ){
                // there was already an answer chosen for this label, but now the user has changed the answer for this label
                // so we need to re-enable the previous option that was selected
                self.$component.find(`select option[value='${self.userSelectionArray[labelIdx]+1}']`).prop('disabled', false);
            }
            self.userSelectionArray[labelIdx] = parseInt(selectedValue)-1; // -1 because of 0 index being unselectable value
            self.$component.find(`select option[value='${selectedValue}']`).prop('disabled', true);
            self.$component.find('select').selectmenu("refresh");

            // close the menu
            // $(this).selectmenu("close");
            self.$component.find('select').selectmenu("close");
            self.checkDone();

            setLabelledBy($(this));
        }
    });
    // self.$component.find('select').selectmenu("open");

    self.$component.find('.ds-clear-btn').on('click keypress', utils.handleClickAndPress(function (e) {
        
        var $this = $(e.currentTarget);
        var optionValAtThisSelect = $(this).closest('.option').find('.option').find('select').val();        
        // index represents the position of the select menu in the html
        var labelIdx = $this.data('ds-clear-btn');
        optionValAtThisSelect = self.userSelectionArray[labelIdx] + 1;
        self.$component.find(`select[data-index="${labelIdx}"]`).prop("selectedIndex", 0);
        self.$component.find(`select option[value='${optionValAtThisSelect}']`).prop('selected', false);
        self.$component.find(`select option[value='${optionValAtThisSelect}']`).prop('disabled', false);

        self.$component.find(`select`).selectmenu("refresh");
        self.userSelectionArray[labelIdx] = undefined;
        
        self.checkDone();
    }));

    function setLabelledBy($select){
        // console.log('setLabelledBy', $select.closest('.label-select-group').find('.ds-label label'));
        // get the label id
        var label = $select.closest('.label-select-group').find('.ds-label label').attr('id');
        var button = $select.selectmenu("widget");
        // get aria-labelledby attribute
        var labelledby = button.attr('aria-labelledby') || '';
        if (labelledby.indexOf(label) == -1) {
            // add the label id to the aria-labelledby attribute if it's not already there
            labelledby = label+' '+labelledby;
        } 
        // set the aria-labelledby attribute
        button.attr('aria-labelledby', labelledby);
    }

}

/**
 * @public
 * @override 
 * @description  This function fired on checkDone
 */
ComponentDropdownsequence.prototype.checkDone = function(){
    var self = this;

    var userAnsweredAll = function(){
        if(self.answerArray.length !== self.userSelectionArray.length){
            return false;
        }
        for(let i = 0; i < self.userSelectionArray.length; i++){
            if(isNaN(self.userSelectionArray[i])){
                return false;
            }
        }
        return true;
    } 

    if (self.userSelectionArray.length == self.$component.find('.option').length && userAnsweredAll()){
        // activate ok
        self.$component.find('.btn.ok').enable();
    } else {
        self.$component.find('.btn.ok').disable();
    }

    // for each clear button, check if an option has been selected in the dropdown next to it, if so disable the clear button, otherwise enable it
    self.$component.find('.ds-clear-btn').each(function(){
        var $this = $(this);
        var optionValAtThisSelect = $(this).closest('.dropdown-clear-btn-group').find('select').val();
        if(isNaN(optionValAtThisSelect)){
            // no option selected
            $this.disable();
        } else {
            // option selected
            $this.enable();
        }
    });
}

