/**
 * @fileoverview This is used to create a Callout component.
 * @class
 * @augments Component
 * @author LIVELOGIC
 * @version 1.0
 */
function ComponentCallout(){
}

ComponentCallout.prototype = Object.create(Component.prototype);

/**
 * @function
 * @description  This function initialiases the component
 */
ComponentCallout.prototype.init = function(_componentController, node, _id, _num){
    Component.prototype.init.call(this, _componentController, node, _id, _num);

    this.managesCompletion = true;
}

/**
 * @function
 * @description  This function sets up the component
 */
ComponentCallout.prototype.setupComponent = function(){
    Component.prototype.setupComponent.call(this);
    
    var self = this;
}

/**
 * @override 
 * @description  This function adds the component HTML
 */
ComponentCallout.prototype.getHTML = function(){
    var self = this;
    
    var htmlStr = '';
    htmlStr += '<div class="svgIcon">'+svgUtils.createSVG('plusminus')+'</div>';
    htmlStr += '<div class="callout-inner">';
    var classStr = this.dataNode.class ? this.dataNode.class : '';
    if (classStr.indexOf('callout-question') != -1 || classStr.indexOf('callout-quotation') != -1){
        htmlStr += '<div class="quotation'+(classStr.indexOf('callout-quotation-small') != -1 ? ' quotationSmall' : '')+'">';
            htmlStr += '<div class="quotation-icon-ldquo">'+svgUtils.createSVG('ldquo')+'</div>';
            htmlStr += '<div class="quotation-icon-rdquo">'+svgUtils.createSVG('rdquo')+'</div>';
            htmlStr += '<div class="quotationContent">';
        }
        htmlStr += this.componentController.generateComponents(this.dataNode);//htmlUtils.getHTMLText(this.dataNode.text[0]);
        
    if (classStr.indexOf('callout-question') != -1 || classStr.indexOf('callout-quotation') != -1){
            htmlStr += '</div>';
        htmlStr += '</div>';
    }
    htmlStr += '</div>';
    return htmlStr;
} 

/**
 * @function
 * @description  This function sets up the component
 */
ComponentCallout.prototype.setupComplete = function(){
    Component.prototype.setupComponent.call(this);
    
    var self = this;
    
    // check if RTL language
    if (languageConfig.languages[currentLanguage].direction == 'rtl'){
        // if (self.$component.hasClass('left')){
        //     self.$component.removeClass('left').addClass('right');
        // }
        // if (self.$component.hasClass('right')){
        //     self.$component.removeClass('right').addClass('left');
        // }
        // if (self.$component.hasClass('top-left')){
        //     self.$component.removeClass('top-left').addClass('top-right');
        // }
        // if (self.$component.hasClass('bottom-left')){
        //     self.$component.removeClass('bottom-left').addClass('bottom-right');
        // }
        // if (self.$component.hasClass('right-top')){
        //     self.$component.removeClass('right-top').addClass('left-top');
        // }
        // if (self.$component.hasClass('left-top')){
        //     self.$component.removeClass('left-top').addClass('right-top');
        // }
        htmlUtils.replaceClassesBasedOnTextDirection(self.$component, {
            'left': 'right', 
            'right': 'left', 
            'top-left': 'top-right', 
            'top-right': 'top-left', 
            'bottom-left': 'bottom-right', 
            'bottom-right': 'bottom-left', 
            'left-top': 'right-top', 
            'right-top': 'left-top'
        });
    }
    
    if (self.$component.hasClass('callout-reveal')){
        self.$component.addClass('action');
        self.$component.on('click', function(){
            if ($(this).hasClass('disabled')){
                return false;    
            }
            $(this).disable();
            
            var $svgIcon = $(this).find('.svgIcon');
            svgUtils.animateSVG('plusminus', $svgIcon);
            $svgIcon.delay(.5).animate({ opacity: 0 },function(){
                $svgIcon.hide();
                self.$component.addClass('shown').find('.callout-inner').slideDown();
                
                // set component complete
                self.componentController.setComponentComplete(self);
            })
        });
        
    } 

    // self.$component.on('inview_anim_start', function(){
    //     console.log('ComponentCallout::inview_anim_start')
    // });

    // if (self.$component.find('.component-carousel')){
    //     self.$component.addClass('hasCarousel');
    // }

    var ldquoTimeline = svgUtils.animateSVG('ldquo', self.$component);
    ldquoTimeline.pause();
    var rdquoTimeline = svgUtils.animateSVG('rdquo', self.$component);
    rdquoTimeline.pause();

    self.$component.on('inview_anim_start', function(e){
        console.log('ComponentCallout::inview_anim_start', e)
        ldquoTimeline.play();
        rdquoTimeline.play();
    });

    self.$component.on('inview_anim_complete', function(){
        // console.log('ComponentCallout::inview_anim_complete');
        if (!self.$component.hasClass('action')){
            // set component complete
            self.componentController.setComponentComplete(self);
        }
    });
}


