/**
 * @fileoverview This is used to create a Highchart_Pie component.
 * @class
 * @augments Component
 * @author LIVELOGIC
 * @version 1.0
 */
function ComponentHighchart_Pie(){
}

ComponentHighchart_Pie.prototype = Object.create(Component.prototype);

/**
 * @function
 * @description  This function initialiases the component
 */
ComponentHighchart_Pie.prototype.init = function(_componentController, node, _id, _num){
    Component.prototype.init.call(this, _componentController, node, _id, _num);

    var self = this;

    //self.managesCompletion = false;

    self.pieColors = ['#ed6335', '#5d9438', '#3d8eae', '#f14e73', '#943888', '#edb735', '#ba1110'];

    Highcharts.setOptions({
        lang: {
            numericSymbols: null,
            thousandsSep: ','
        }
    });
}

/**
 * @function
 * @description  This function adds the component HTML
 */
ComponentHighchart_Pie.prototype.getHTML = function(){
    var self = this;   
    
    var htmlStr = '';
    
    htmlStr += '<figure class="highcharts-figure">';// role="application">';
        htmlStr += '<div id="'+self.componentID+'-pie-chart"></div>';
    htmlStr += '</figure>';
            
    
    return htmlStr;
}

/**
 * @function
 * @description  This function sets up the component
 */
ComponentHighchart_Pie.prototype.setupComponent = function(){
    Component.prototype.setupComponent.call(this);
    
    var self = this;

    self.pieData = [];
    for (var i = 0; i < self.dataNode.data[0].item.length; i++) {
        self.pieData.push({name: self.dataNode.data[0].item[i].Text, y: parseInt(self.dataNode.data[0].item[i].val)})
    }

    self.chartOptions = {
        // chart: {
        //     plotBackgroundColor: null,
        //     plotBorderWidth: null,
        //     plotShadow: false,
        //     type: 'pie'
        // },
        title: {
            text: self.dataNode.title[0].Text,
            align: 'center'
        },
        colors: self.pieColors,
        // tooltip: {
        //     pointFormat: '<b>{point.percentage:.0f}%</b>'
        // },        
        accessibility: {
            keyboardNavigation: {
                focusBorder: {
                    margin: 4,
                    style: {
                        borderWidth: 2,
                        borderRadius: 0,
                        color: '#DB0011' // hsbcred
                    }
                }
            }
        //     point: {
        //         valueSuffix: '%'
        //     }
        },
        plotOptions: {
            pie: {
                // allowPointSelect: true,
                cursor: 'pointer',
                // dataLabels: {
                //     enabled: true,
                //     format: '<b>{point.name}</b>: {point.percentage:.0f}%'
                // }
            }
        },
        series: [
            {
                id: self.componentID+'-data',
                type: 'pie',
                name: self.dataNode.data[0].name[0].Text,
                // colorByPoint: true,
                data: self.pieData,
                size: 200,
            }
        ]
    }
    
    // description
    if (self.dataNode.caption){
        self.chartOptions.caption = {
            useHTML: true,
            text: htmlUtils.getHTMLText(self.dataNode.caption[0])
        }
        // self.chartOptions.caption.align = self.dataNode.caption[0].align || 'center'; // doesnt work with useHTML: true
    }
    if (self.dataNode.description){
        self.chartOptions.accessibility.description = self.dataNode.description[0].Text
    }
    // show percentage - this will convert values to %
    if (self.dataNode.showPercentage){
        self.dataNode.tooltipFormat = '<b>{point.percentage:.0f}%</b>';
        self.dataNode.dataLabelsFormat = '<b>{point.percentage:.0f}%</b>';
        self.dataNode.accessibilityPointSuffix = '%';
    }
    // tooltip pointFormat
    if (self.dataNode.tooltipFormat){
        self.chartOptions.tooltip = {
            pointFormat: self.dataNode.tooltipFormat //'<b>{point.percentage:.0f}%</b>'
        }
    }
    // plotOptions dataLabels format
    if (self.dataNode.dataLabelsFormat){
        self.plotOptions.pie.dataLabels = {
            enabled: true,
            format: self.dataNode.dataLabelsFormat // '<b>{point.name}</b>: {point.percentage:.0f}%'
        }
    }
    // accessibility valueSuffix e.g. %
    if (self.dataNode.accessibilityPointSuffix){
        self.plotOptions.accessibility = {
            point: {
                valueSuffix: self.dataNode.accessibilityPointSuffix // '%'
            }
        }
    }
    // // Pie chart
    // self.chart = Highcharts.chart(self.componentID+'-pie-chart', self.chartOptions);
}

/**
 * @function
 * @description  This function fires when sets up complete
 */
ComponentHighchart_Pie.prototype.setupComplete = function(){
    Component.prototype.setupComplete.call(this);
    
    var self = this;   
}

/**
 * @public
 * @override 
 * @description  This function is called on unload
 */
ComponentHighchart_Pie.prototype.unload = function(){
    var self = this;    
    
    if (self.chart){
        self.chart.destroy();
    }
}

/**
 * @public
 * @override 
 * @description  This function fired when component is displayed
 */
ComponentHighchart_Pie.prototype.componentDisplayed = function(){
    Component.prototype.componentDisplayed.call(this);

    var self = this;    
    
    // set component complete
    //self.componentController.setComponentComplete(self);

    // Pie chart
    self.chart = Highcharts.chart(self.componentID+'-pie-chart', self.chartOptions);

    if (self.chart){
        self.chart.reflow();
    }
}
/**
 * @public
 * @override 
 * @description  This function is called on browser resize
 */
ComponentHighchart_Pie.prototype.resize = function(){
    var self = this;    
}

/**
 * @public
 * @override 
 * @description  This function is called on device breakpoint changed
 */
 ComponentHighchart_Pie.prototype.breakpointChanged = function(){
    var self = this;
 }
