/**
 * @fileoverview This is used to create an ParallaxHeader component.
 * @class
 * @augments Component
 * @author LIVELOGIC
 * @version 1.0
 */
function ComponentParallaxHeader(){
}

ComponentParallaxHeader.prototype = Object.create(Component.prototype);

/**
 * @function
 * @description  This function initialiases the component
 */
ComponentParallaxHeader.prototype.init = function(_componentController, node, _id, _num){
    Component.prototype.init.call(this, _componentController, node, _id, _num);

    this.managesCompletion = false;
}

/**
 * @function
 * @description  This function adds the component HTML
 */
ComponentParallaxHeader.prototype.getHTML = function(){
    var self = this;

    var htmlStr = '';
    
    htmlStr += '<div class="buildup-container">';
        htmlStr += '<div class="buildup-layer-wrap">';
        for (var i=0; i < self.dataNode.layer.length; i++){
            if (self.dataNode.layer[i].src){
                // create an image to use as a guide for setting aspect ratios
                var path = utils.getImagePath(self.dataNode.layer[i].src);
                htmlStr += '<img class="buildup-layer-temp-image" src="'+path+'">';
                break;
            }
        }

        var bkgPosition = self.dataNode.backgroundPosition ? ' background-position: '+self.dataNode.backgroundPosition+';' : ''; 
        for (var i=0; i < self.dataNode.layer.length; i++){

            var styleStr = ''
            if (self.dataNode.layer[i].src){
                // this is an image
                var path = utils.getImagePath(self.dataNode.layer[i].src);
                styleStr = 'style="background-image: url('+path+');'+(self.dataNode.layer[i].backgroundPosition ? ' background-position: '+self.dataNode.layer[i].backgroundPosition+';' : bkgPosition)+'"';
                htmlStr += '<div class="buildup-layer buildup-image-layer buildup-layer-'+i+'" '+styleStr+(self.dataNode.layer[i].speed != undefined ? ' data-speed="'+self.dataNode.layer[i].speed+'"' : '')+'></div>';
            } else {
                // this could be text/html content
                htmlStr += '<div class="buildup-layer buildup-content-layer buildup-layer-'+i+'"'+(self.dataNode.layer[i].speed ? ' data-speed="'+self.dataNode.layer[i].speed+'"' : '')+'>';
                    htmlStr += self.componentController.generateComponents(self.dataNode.layer[i]);
                htmlStr += '</div>';
            }

            
        }
        htmlStr += '</div>';
    htmlStr += '</div>';

    var columnsStr = self.dataNode.content ? responsiveUtils.getBootstrapColumnsStrForNode(self.dataNode.content[0]) : '';
    if (self.dataNode.title){            
        htmlStr += '<div class="overlay title-overlay">';
        //     htmlStr += '<div class="overlayContent">';                        
        //         htmlStr += txt;
        //     htmlStr += '</div>';
        // htmlStr += '</div>';
            htmlStr += '<div class="container">';
                htmlStr += '<div class="row">';
                    htmlStr += '<div class="'+(columnsStr != '' ? ' '+columnsStr : 'col-md-9')+'">';
                        htmlStr += '<div class="overlayContent">';                        
                            htmlStr += htmlUtils.getHTMLText(self.dataNode.title[0]);
                        htmlStr += '</div>';
                    htmlStr += '</div>';
                htmlStr += '</div>';
            htmlStr += '</div>';
        htmlStr += '</div>';
    }
    // var txt = htmlUtils.getHTMLText();
    if (self.dataNode.content){
        htmlStr += '<div class="overlay content-overlay">';
            htmlStr += '<div class="container">';
                htmlStr += '<div class="row">';
                    htmlStr += '<div class="'+(columnsStr != '' ? ' '+columnsStr : 'col-md-9')+'">';
                        htmlStr += '<div class="overlayContent parallelogram anim fadeIn">';                        
                            htmlStr += self.componentController.generateComponents(self.dataNode.content[0]);
                        htmlStr += '</div>';
                    htmlStr += '</div>';
                htmlStr += '</div>';
            htmlStr += '</div>';
        htmlStr += '</div>';
    }
    // htmlStr += '</div>';

    
    htmlStr += `<div class="header-btn-wrap">
                    <button class="btn header-btn" aria-label="${courseData.getAccessibleLabel('scrollToContentView')}">${svgUtils.createSVG('arrow-down')}</button>
                </div>`;
    
    return htmlStr;
} 


/**
 * @function
 * @description  This function sets up the component
 */
ComponentParallaxHeader.prototype.setupComponent = function(){
    Component.prototype.setupComponent.call(this);

    var self = this;
    
    if (this.dataNode.backgroundImage){
        this.$component.addClass('hasImage');

    }

    // keep at top
    self.$component.on('inview_anim_start', function(){

            // apply parallax effect to any element with a data-speed attribute
        gsap.to("[data-speed]", {
            y: (i, el) => (1 - parseFloat(el.getAttribute("data-speed"))) * ScrollTrigger.maxScroll(window) ,
            ease: "none",
            scrollTrigger: {
                start: 0,
                end: "max",
                invalidateOnRefresh: true,
                scrub: 0
            }
        });

        // if (self.$component.hasClass('fullscreen')){
        //     setTimeout(function(){

        //         // autoscroll
        //         navigationController.scrollIntoView(self.$component.next('.section'), {duration: 2, offsetY: self.$component.find('.overlay').height(), ease: 'power2.inOut'});
        //     }, 3000);
        // }

        // content-overlay
        
        // // if ()
        // // pin the title on scroll
        // self.$scrolltrigger = ScrollTrigger.create({
        //     trigger: self.$component.find('.title-overlay .overlayContent'),//$stickys.eq(index).find('.sticky-content'),
        //     start: "top top+=50",
        //     end: "max",
        //     //markers: true,
        //     pin: true,
        //     pinSpacing: false
        // });
    });

    var timeline = new gsap.timeline();

    var $layers = self.$component.find('.buildup-layer');

    
    for (var i=0; i < self.dataNode.layer.length; i++){
        var gsapObj = {
            // x: self.dataNode.layer[i].x || undefined,
            // y: self.dataNode.layer[i].y || undefined,
            scale: self.dataNode.layer[i].scale || undefined,
            rotation: self.dataNode.layer[i].rotation || undefined,
            xPercent: self.dataNode.layer[i].xPercent || self.dataNode.layer[i].x || undefined,
            yPercent: self.dataNode.layer[i].yPercent || self.dataNode.layer[i].y || undefined,
            delay: (self.dataNode.layer[i].delay || '0'),
            duration: (self.dataNode.layer[i].duration || '5'),
            // ease: (self.dataNode.layer[i].ease || 'power3')
            ease: (self.dataNode.layer[i].ease || 'power3')
            // ease: (self.dataNode.layer[i].ease || CustomEase.create("custom", "M0,0,C0.126,0.382,0.256,0.684,0.414,0.832,0.606,1.012,0.818,1.001,1,1"))
            // ease: (self.dataNode.layer[i].ease || CustomEase.create("custom", "M0,0,C0.126,0.382,0.692,0.086,0.85,0.234,1.042,0.414,0.818,1.001,1,1"))
            
            
        };
        
        // for RTL languages, reverse the animation so flip the X values
        if (languageConfig.languages[currentLanguage].direction == 'rtl' && gsapObj.xPercent){
            if (gsapObj.xPercent.indexOf('-=') != -1){
                gsapObj.xPercent = gsapObj.xPercent.split('-=').join('+=');
            } else if (gsapObj.xPercent.indexOf('+=') != -1){
                gsapObj.xPercent = gsapObj.xPercent.split('+=').join('-=');
            }
        }

        //var direction = 
        var $layer = $layers.eq(i);
        timeline.from($layer, gsapObj, '<');
    }

    

    // var timeline = gsap.timeline({
    //     scrollTrigger: {
    //         trigger: self.$component.find('.overlayContent'),//$stickys.eq(index).find('.sticky-content'),
    //         start: "top top+=50",
    //         end: "max",
    //         //markers: true,
    //         pin: true,
    //         pinSpacing: false
    //     }
    // });
    // timeline.to(self.$component.find('.overlayContent'), {scale:.5});

    $(document).on(courseEvent.SLIDE_SHOW_CONTENT, function(){
        // lets refresh the scroll triggers
        var scrollTriggers = ScrollTrigger.getAll();
        for(var i = 0; i < scrollTriggers.length; i++){
            scrollTriggers[i].refresh();
        }
    });
}


/**
 * @function
 * @description  This function fires when sets up complete
 */
ComponentParallaxHeader.prototype.setupComplete = function(){
    Component.prototype.setupComplete.call(this);
        
    var self = this; 

    self.$component.find('.header-btn').on('click keypress', utils.handleClickAndPress(function(e){
        var $this = $(e.currentTarget);
        navigationController.scrollIntoView($this.closest('.slide').next(), 'start');    
    }));

    
}


/**
 * @public
 * @override 
 * @description  This function is called on unload
 */
 ComponentParallaxHeader.prototype.unload = function(){
    var self = this;    

    var scrollTriggers = ScrollTrigger.getAll()
    for(var i = 0; i < scrollTriggers.length; i++){
        scrollTriggers[i].kill();
    }
}

/**
 * @public
 * @override 
 * @description  This function fired when component is displayed
 */
ComponentParallaxHeader.prototype.componentDisplayed = function(){
    Component.prototype.componentDisplayed.call(this);

//    console.log('ComponentImage.prototype.componentDisplayed');

    
}




