/**
 * @fileoverview This is used to create a Accordion component.
 * @class
 * @augments Component
 * @author LIVELOGIC
 * @version 1.0
 */
function ComponentAccordion(){
}

ComponentAccordion.prototype = Object.create(Component.prototype);

/**
 * @function
 * @description  This function initialiases the component
 */
ComponentAccordion.prototype.init = function(_componentController, node, _id, _num){
    Component.prototype.init.call(this, _componentController, node, _id, _num);

    var self = this;

    self.managesCompletion = true;
}

/**
 * @function
 * @description  This function adds the component HTML
 */
ComponentAccordion.prototype.getHTML = function(){
    var self = this;   
    
    var htmlStr = '';
    
    // var allowMultiple = $accordion.data('allow-multiple') == true;
    // Allow for each toggle to both open and close individually
    // var allowToggle = (allowMultiple) ? allowMultiple : $accordion.data('allow-toggle') == 'true';

    htmlStr += '<div id="'+self.id+'-accordion" class="accordion-group" data-allow-multiple="'+(self.dataNode.multiple=='true' ? 'true' : 'false')+'" data-allow-toggle="'+(self.dataNode.toggle=='false' ? 'false' : 'true')+'">';
    for (var i = 0; i < self.dataNode.item.length; i++){

        htmlStr += '<div id="'+self.id+'-item'+i+'" class="accordion-item">';
            htmlStr += '<button class="accordion-button category-title" role="button" aria-controls="'+self.id+'-collapse'+i+'" aria-expanded="false">';
                htmlStr += '<div class="accordion-icon">'+svgUtils.createSVG('chevron-down')+'</div>';
                htmlStr += '<div class="accordion-heading">'
                    htmlStr += `<${self.dataNode.item[i].title[0].headingTag ? self.dataNode.item[i].title[0].headingTag : 'h3' } id="`+self.id+'-heading'+i+'">'+self.dataNode.item[i].title[0].Text+`</${self.dataNode.item[i].title[0].headingTag ? self.dataNode.item[i].title[0].headingTag : 'h3' }>`;
                htmlStr += '</div>';
            htmlStr += '</button>';
            htmlStr += '<div id="'+self.id+'-collapse'+i+'" class="accordion-reveal collapsed" role="region" aria-labelledby="'+self.id+'-heading'+i+'">';
                htmlStr += self.componentController.generateComponents(self.dataNode.item[i].reveal[0]);
            htmlStr += '</div>';
        htmlStr += '</div>';
    }
    htmlStr += '</div>';
    
    return htmlStr;
}

/**
 * @function
 * @description  This function sets up the component
 */
ComponentAccordion.prototype.setupComponent = function(){
    Component.prototype.setupComponent.call(this);
    
    var self = this;   
}

/**
 * @function
 * @description  This function fires when sets up complete
 */
ComponentAccordion.prototype.setupComplete = function(){
    Component.prototype.setupComplete.call(this);
    
    var self = this;   

    // from https://www.w3.org/TR/wai-aria-practices/examples/accordion/accordion.html
    var $accordion = self.$component.find('.accordion-group');
    var $accordionItems = $accordion.find('.accordion-item');

    // Allow for multiple accordion sections to be expanded at the same time
    var allowMultiple = $accordion.data('allow-multiple') == true;
    // Allow for each toggle to both open and close individually
    var allowToggle = (allowMultiple) ? allowMultiple : $accordion.data('allow-toggle') == true;
    // var allowToggle = $accordion.data('allow-toggle') == true;
    console.log('allowToggle', $accordion.data('allow-toggle'), allowToggle)
    console.log('allowMultiple', allowMultiple)
    console.log(allowMultiple, $accordion.data('allow-toggle') == true)

    // Create the array of toggle elements for the accordion group
    var triggers = Array.prototype.slice.call($accordion[0].querySelectorAll('.accordion-button'));
    var panels = Array.prototype.slice.call($accordion[0].querySelectorAll('.accordion-reveal'));


    //  accordion.addEventListener('click', function (event) {
    self.$component.find('.accordion-button').on('click', function (event) {
        var target = $(this)[0]; //event.target;
        //console.log('click', target)

        $(target).closest('.accordion-item').addClass('done');

        if (target.classList.contains('accordion-button')) {
            // Check if the current toggle is expanded.
            var isExpanded = target.getAttribute('aria-expanded') == 'true';
            var active = $accordion[0].querySelector('[aria-expanded="true"]');

            // without allowMultiple, close the open accordion
            if (!allowMultiple && active && active !== target) {
                // Set the expanded state on the triggering element
                active.setAttribute('aria-expanded', 'false');
                // Hide the accordion sections, using aria-controls to specify the desired section
                //document.getElementById(active.getAttribute('aria-controls')).setAttribute('hidden', '');
                $('#' + active.getAttribute('aria-controls')).slideUp(function () {
                    $(this).addClass('collapsed');
                });

                // When toggling is not allowed, clean up disabled state
                if (!allowToggle) {
                    active.removeAttribute('aria-disabled');
                }
            }

            if (!isExpanded) {
                // Set the expanded state on the triggering element
                target.setAttribute('aria-expanded', 'true');
                // Hide the accordion sections, using aria-controls to specify the desired section
                //document.getElementById(target.getAttribute('aria-controls')).removeAttribute('hidden');
                $('#' + target.getAttribute('aria-controls')).slideDown(function () {
                    $(this).removeClass('collapsed');
                    // let's tell any components they are now displayed
                    self.componentController.slideObj.showElementContents($(this));
                });

                // If toggling is not allowed, set disabled state on trigger
                if (!allowToggle) {
                    target.setAttribute('aria-disabled', 'true');
                }
            } else if (allowToggle && isExpanded) {
                console.log('close')
                // Set the expanded state on the triggering element
                target.setAttribute('aria-expanded', 'false');
                // Hide the accordion sections, using aria-controls to specify the desired section
                //document.getElementById(target.getAttribute('aria-controls')).setAttribute('hidden', '');
                $('#' + target.getAttribute('aria-controls')).slideUp(function () {
                    $(this).addClass('collapsed');
                });
            }

            event.preventDefault();

            self.$component.find('.accordion-button').each(function(){
                // console.log($(this).attr('aria-expanded'))
                if ($(this).attr('aria-expanded') == 'false'){
                    svgUtils.animateSVG('chevron-down', $(this));
                } else {
                    svgUtils.animateSVG('chevron-down-reverse', $(this));
                }
            });

            console.log('setComponentComplete', self.id)
            self.componentController.setComponentComplete(self);
        }
    });

    // Bind keyboard behaviors on the main accordion container
    //accordion.addEventListener('keydown', function (event) {
    self.$component.find('.accordion-button').on('keydown', function (event) {
        console.log('on key down');

        var target = event.target;
        var key = event.which.toString();

        // var isExpanded = target.getAttribute('aria-expanded') == 'true';
        // var allowToggle = (allowMultiple) ? allowMultiple : $accordion.data('allow-toggle') == 'true';

        // 33 = Page Up, 34 = Page Down
        var ctrlModifier = (event.ctrlKey && key.match(/33|34/));

        // Is this coming from an accordion header?
        if (target.classList.contains('accordion-button')) {
            // Up/ Down arrow and Control + Page Up/ Page Down keyboard operations
            // 38 = Up, 40 = Down
            if (key.match(/38|40/) || ctrlModifier) {
                var index = triggers.indexOf(target);
                var direction = (key.match(/34|40/)) ? 1 : -1;
                var length = triggers.length;
                var newIndex = (index + length + direction) % length;

                triggers[newIndex].focus();

                event.preventDefault();
            } else if (key.match(/35|36/)) {
                // 35 = End, 36 = Home keyboard operations
                switch (key) {
                    // Go to first accordion
                    case '36':
                        triggers[0].focus();
                        break;
                        // Go to last accordion
                    case '35':
                        triggers[triggers.length - 1].focus();
                        break;
                }
                event.preventDefault();

            }

        }
    });

    // These are used to style the accordion when one of the buttons has focus
    $accordion[0].querySelectorAll('.accordion-button').forEach(function (trigger) {

        trigger.addEventListener('focus', function (event) {
            $accordion[0].classList.add('focus');
        });

        trigger.addEventListener('blur', function (event) {
            $accordion[0].classList.remove('focus');
        });

    });

    // Minor setup: will set disabled state, via aria-disabled, to an
    // expanded/ active accordion which is not allowed to be toggled close
    if (!allowToggle) {
        // Get the first expanded/ active accordion
        var expanded = $accordion[0].querySelector('[aria-expanded="true"]');

        // If an expanded/ active accordion is found, disable
        if (expanded) {
            expanded.setAttribute('aria-disabled', 'true');
        }
    }
}

