/**
 * @fileoverview This is used to create a Panel component.
 * @class
 * @augments Component
 * @author LIVELOGIC
 * @version 1.0
 */
function ComponentPanel(){
}

ComponentPanel.prototype = Object.create(Component.prototype);

/**
 * @function
 * @description  This function initialiases the component
 */
ComponentPanel.prototype.init = function(_componentController, node, _id, _num){
    Component.prototype.init.call(this, _componentController, node, _id, _num);

    //this.managesCompletion = false;
}

/**
 * @function
 * @description  This function sets up the component
 */
ComponentPanel.prototype.setupComponent = function(){
    Component.prototype.setupComponent.call(this);
    
    var self = this;   
    
    //console.log('PANEL', this.$component)
    var svg = this.dataNode.svg;
    if (svg){
        var timeline = svgUtils.animateSVG(svg, this.$component);
        timeline.pause();
        this.$component.on('inview_anim_complete', function(){
            //console.log('inview_anim_complete', self.$component, timeline);
            timeline.play();
        });
    }
}

/**
 * @function
 * @description  This function adds the component HTML
 */
ComponentPanel.prototype.getHTML = function(){
    var htmlStr = '';
    var hasIcon = false;
    if (this.dataNode.svg){
        hasIcon = true;
    }

    htmlStr += '<div class="panel-inner'+(hasIcon ? ' has-icon' : '')+'">';
        if (hasIcon){
            htmlStr += '<div class="panel-icon svg-icon">'+svgUtils.createSVG(this.dataNode.svg)+'</div>';
        }
        htmlStr += '<div class="panel-content">';
            htmlStr += this.componentController.generateComponents(this.dataNode);
        htmlStr += '</div>';
    htmlStr += '</div>';
    
    //console.log('PANEL getHTML', this.id)
    
    return htmlStr;
}

/**
 * @public
 * @override 
 * @description  This function is called on unload
 */
ComponentPanel.prototype.unload = function(){
    var self = this;    
}

/**
 * @public
 * @override 
 * @description  This function fired when component is displayed
 */
ComponentPanel.prototype.componentDisplayed = function(){
    Component.prototype.componentDisplayed.call(this);

    var self = this;    
    
    
    
    // set component complete
    //self.componentController.setComponentComplete(self);
}
/**
 * @public
 * @override 
 * @description  This function is called on browser resize
 */
ComponentPanel.prototype.resize = function(){
    var self = this;    
}
