/**
 * @fileoverview This is used to create a GSFMenu component.
 * @class
 * @augments Component
 * @author LIVELOGIC
 * @version 1.0
 */
function ComponentGSFMenu(){
}

ComponentGSFMenu.prototype = Object.create(Component.prototype);

/**
 * @function
 * @description  This function adds the component HTML
 */
ComponentGSFMenu.prototype.init = function(_componentController, node, _id, _num){
    Component.prototype.init.call(this, _componentController, node, _id, _num);

    this.managesCompletion = true;
}

/**
 * @function
 * @description  This function adds the component HTML
 */
ComponentGSFMenu.prototype.getHTML = function(){
    var self = this;
//    console.log('ComponentButton.prototype.getHTML', this.dataNode.text[0].Text);
    var htmlStr = '';
    
    var menuArray = courseData.data.settings[0].menu;
    self.moduleCount = 0;
    self.incompleteCount = 0;
    var firstMandatoryModule = 1000;
    var firstLockedModule = 1000;
    
    self.assessmentModuleCount = 0;
    self.assessmentMandatoryModuleCount = 0;
    
    //var POST_ASSESSMENT_MODULE_NO = 6; 
    
    for (var i = 0; i < menuArray.length; i++){
        if (menuArray[i]){ 
//            if (courseData.navData.module[i].disabled || (i == POST_ASSESSMENT_MODULE_NO && preAssessment.data.perc == 100)){
            if (courseData.navData.module[i].disabled || !storageController.isModuleActive(i) || !navigationController.isModuleValidForRole(i)){
                // skip this module
                continue;
            }
            var moduleData = courseData.getModuleData(i);
            if (moduleData.assessmentBank){
                self.assessmentModuleCount ++;
                if (storageController.isModuleMandatory(i)){
                    self.assessmentMandatoryModuleCount ++;
                }
            }
            
            self.moduleCount ++;
            if (storageController.isModuleMandatory(i)){
                if (!storageController.isModuleComplete(i)){
                    firstMandatoryModule = Math.min(firstMandatoryModule, i);
                    self.incompleteCount ++;

                    // is module acccessed in linear order
                    var isLinear = (courseData.getModuleData(i).linear == "false") ? false : true;
                    if (isLinear){
                        firstLockedModule = Math.min(firstLockedModule, i);
                    }
                }    
            }
        }
    }
    htmlStr += '<div class="row gsfmenu-flex-row">';
    console.log('ComponentGSFMenu', firstLockedModule, firstMandatoryModule, navigationConfig.developerUnlock)
    for (var i = 0; i < menuArray.length; i++){
        if (menuArray[i]){    
            var menuNum = i;//courseData.data.settings[0].menu[i]
            
            // console.log('ComponentGSFMenu', storageController.isModuleActive(i), navigationController.isModuleValidForRole(i))
            if (storageController.isModuleActive(i) && navigationController.isModuleValidForRole(i)){
//            if (menuNum != POST_ASSESSMENT_MODULE_NO || (menuNum == POST_ASSESSMENT_MODULE_NO && preAssessment.data.perc < 100)){
                // if score 100% on pre assessment, dont show post assessment

                // from navigation.xml
                var moduleClassStr = "";
                var ariaDisabled = 'false';
                var isModuleComplete = false;
                if (storageController.isModuleComplete(i)){
                    isModuleComplete = true;
                    moduleClassStr += ' done';
                    if (courseData.navData.module[i].return == 'false'){
                        moduleClassStr += ' locked disabled';
                        ariaDisabled = 'true';
                    }
                } else {
                    if (storageController.isModuleMandatory(i)){
                         moduleClassStr += ' mandatory';
                    } 
                    if (courseData.navData.module[menuNum].disabled){
                        // force module to be disabled
                        moduleClassStr += ' locked disabled';
                        ariaDisabled = 'true';
                    } else if ((i > firstLockedModule || (i >= firstLockedModule && i > firstMandatoryModule)) && !navigationConfig.developerUnlock && navigationConfig.moduleLock){
                        moduleClassStr += ' locked disabled';
                        ariaDisabled = 'true';
                    } 
                }

                // get first page
                var firstPageInModule = navigationController.getFirstPageValidForRole(i);
                // console.log('getFirstPageValidForRole', firstPageInModule)

                htmlStr += '<div class="col-sm-4">';
                    htmlStr += '<div class="menuItemWrap anim fadeInRight" data-module="'+menuNum+'" data-page="'+firstPageInModule+'">';
                    // var path = utils.getImagePath(courseData.data.module[menuNum].img);
                        // htmlStr += '<div class="menu-card-body">';
                        htmlStr += '<div class="menu-card-body" style="">';
                            // htmlStr += '<div class="">';
                            htmlStr += '<div class=" a-button module-btn'+moduleClassStr+'" role="button" tabindex="0" aria-disabled="'+ariaDisabled+'">';
        //                        htmlStr += '<div class="tile-bar"></div>';
        //                        htmlStr += '<div class="row">';
        //                            htmlStr += '<div class="col-xs-3 col-sm-6">';
        //                            htmlStr += '<div class="col-xs-6">';
                                        var path = utils.getImagePath(courseData.data.module[menuNum].img);
                                        var inactive_path = utils.getImagePath(courseData.data.module[menuNum].imgGrey);
                                        // htmlStr += '<div class="image-container">';
                                            htmlStr += '<div aria-hidden="true" class="image-wrap" >';
                                                htmlStr += '<img class="img-responsive active-image" src="'+path+'"/>';
                                                htmlStr += '<img class="img-responsive inactive-image" src="'+inactive_path+'"/>';
                                                // htmlStr += '<div class="image-overlay" style="background-image: url('+path+')"></div>';
                                                htmlStr += '<div class="image-overlay"></div>';
                                            htmlStr += '</div>';
                                        // htmlStr += '</div>';
                                    //htmlStr += '</div>';
        //                            htmlStr += '<div class="col-xs-9 col-sm-6">';
        //                            htmlStr += '<div class="col-xs-6">';
                                        htmlStr += '<div class="module">';
        //                                    htmlStr += '<div class="a-button module-tile '+moduleClassStr+'">';
    //                                        htmlStr += '<div class="module-tile '+moduleClassStr+'">';
                                            htmlStr += '<div class="module-tile">';
                                                // htmlStr += '<div class="tile-bar"></div>';
                                                htmlStr += '<div class="tile-title"><span>'+courseData.data.module[menuNum].title[0].Text+'</span></div>';
                                                htmlStr += '<span class="sr-only">'+(isModuleComplete ? ' '+courseData.getAccessibleLabel('complete') : '')+'</span>';
                                                htmlStr += '<div class="status" aria-hidden="true">';
                                                    htmlStr += '<div class="status-icon"></div>';
                                                htmlStr += '</div>';
                                                if (courseData.data.module[menuNum].info){
                                                htmlStr += '<div class="tile-content">'+htmlUtils.getHTMLText(courseData.data.module[menuNum].info[0])+'</div>';
                                                }
                                                if (courseData.data.module[menuNum].time){
                                                htmlStr += '<div class="tile-time">'+htmlUtils.getHTMLText(courseData.data.module[menuNum].time[0])+'</div>';
                                                }
                                            htmlStr += '</div>';
                                            
                                        htmlStr += '</div>';
        //                            htmlStr += '</div>';
                                // htmlStr += '</div>';
                            htmlStr += '</div>';
                            if (menuArray[i].submenu.length){
                            htmlStr += '<div class="col-sm-12 col-md-9 col-lg-6">';
                                htmlStr += '<div class="submodulesWrap">';
                                    htmlStr += '<div class="submodules">';
                                        // htmlStr += '<div class="row">';
                                        for (var j = 0; j < menuArray[i].submenu.length; j++){
                                            var isPageComplete = storageController.isPageComplete(navigationController.getModulePageIndex(i, menuArray[i].submenu[j].page));
                                            // htmlStr += '<div class="col-lg-6">';
                                                htmlStr += '<a class="submodule-btn'+(isPageComplete ? ' done' : '')+'" data-module="'+i+'" data-page="'+menuArray[i].submenu[j].page+'" data-slide="'+menuArray[i].submenu[j].slide+'">';
                                                    htmlStr += '<div class="submodule-title">'+menuArray[i].submenu[j].title+'</div>';
                                                    htmlStr += '<div class="status"><div class="status-icon"></div></div>';
                                                htmlStr += '</a>';
                                            // htmlStr += '</div>';
                                        }
                                        // htmlStr += '</div>';
                                    htmlStr += '</div>';
                                htmlStr += '</div>';
                            htmlStr += '</div>';
                            }
                        htmlStr += '</div>';
                    htmlStr += '</div>';
                htmlStr += '</div>';
            }
        }
    }
    htmlStr += '</div>';
    return htmlStr;
}


/**
 * @public
 * @override 
 * @description  This function fired when component is displayed
 */
ComponentGSFMenu.prototype.componentDisplayed = function(){
    Component.prototype.componentDisplayed.call(this);

    var self = this;
    
    //console.log(self.$component.closest('.slide').find('.allMandatory'))
    if (self.assessmentMandatoryModuleCount  == self.assessmentModuleCount){
        self.$component.closest('.slide').find('.allMandatory').removeClass('hidden');
    } else if (self.assessmentMandatoryModuleCount > 0){
        self.$component.closest('.slide').find('.someMandatory').removeClass('hidden');
    } else {
        self.$component.closest('.slide').find('.noneMandatory').removeClass('hidden');
    }
    
    self.$component.find('.module-btn').on('click keypress', utils.handleClickAndPress(function (e) {
        var $this = $(e.currentTarget);
        if ($this.hasClass('disabled') || $this.hasClass('clicked')){
            return false;
        }

        var $submodules = $this.closest('.menuItemWrap').find('.submodules');
        if ($submodules.length){
            // has submodules
            $submodules.toggleClass('shown');
            if (!responsiveUtils.isCurrentViewport('lg')){
                $submodules.slideToggle();
            }
        } else {
            $this.addClass('clicked');
            // go to module
            var $module = $this.closest('.menuItemWrap');
            navigationController.changePageToData($module.data('module'), $module.data('page'), 0);
        }
    
    })); 

    self.$component.find('.submodule-btn').on('click keypress', utils.handleClickAndPress(function (e) {
        var $this = $(e.currentTarget);
        if ($this.hasClass('disabled') || $this.hasClass('clicked')){
            return false;
        }

        $this.addClass('clicked');
        navigationController.changePageToData($this.data('module'), $this.data('page'), $this.data('slide'));
    
    })); 
    
}

/**
 * @public
 * @override 
 * @description  This function fired on resize
 */
ComponentGSFMenu.prototype.resize = function(){
    var self = this;

    self.$component.find('.module-btn').each(function(index){
        var $this = $(this);
        if (!self.splitText){
            self.splitText = [];
        }
        if (self.splitText[index]){
            self.splitText[index].revert();
        } 
        self.splitText[index] = new SplitText($this.find('.tile-title > span'), {type: 'lines', tag: 'span', linesClass: ''});
    });
}
    

