/**
 * @fileoverview This is used to create a component.
 * @class
 * @author LIVELOGIC
 * @version 1.0
 */
function Component(){
    var self = this;
    this.id = '';
    this.dataNode;
    this.componentController;
    
//    this.$component;
//    
//    this.isComplete = false;
//    this.draggable = false;
////    
////    
//    this.actions = [];
//    
//    this.showOnAllComplete = false;
}

Component.prototype = {

    /**
    * @function
    * @description This function initialiases the component
    */
    init: function(_componentController, node, _id, _num){
        // console.log('COMP SETUP', _id, _num);
        this.id = _id;
        this.num = _num;
        
        this.componentController = _componentController;
        
        this.slideIndex = (this.componentController.slideObj instanceof Slide) ? this.componentController.slideObj.slideNum : 'NOSLIDE';
        this.slideID = (this.componentController.slideObj instanceof Slide) ? this.componentController.slideObj.slideID : 'NOSLIDE';
        //console.log('Component.slideID', this.slideID);
        this.componentID = this.slideID+'-'+this.id;
        
        this.$component;
        
        this.dataNode = node;

        this.roles = [];
        
        this.draggable = false;

        this.isSetup = false;
        
        this.managesCompletion = false;
        this.isComplete = (this.dataNode.complete == "true") ? true : false;
        this.animsComplete = this.isComplete;
        
        this.actions = [];
        
        this.xsCloneRevealAlongside = (this.dataNode.xsCloneRevealAlongside == true) ? true : false;
        
        this.showOnComplete = false;
        this.hideOnComplete = false;
        this.visibleOnComplete = false;
        this.invisibleOnComplete = false;
        this.enableOnComplete = false;
        
        this.showOnComponentsComplete = false;
        this.visibleOnComponentsComplete = false;
        this.revealOnComponentsCompleteArray = [];

        this.completeOnChildrenComplete = false;
        this.parentComponent = false;
        this.childComponents = [];

        // timing for SCORM
        this.timeDisplayed = '';
        
        // question info
        this.showMarking = this.dataNode.showMarking ? stringToBoolean(this.dataNode.showMarking) : true;
        this.showAnswers = this.dataNode.showAnswers ? stringToBoolean(this.dataNode.showAnswers) : true;

        // ASSESSMENT INFO
        this.isAssessmentQuestion = false;
        this.assessmentID = '';

        this.assessmentController;
        if (node.assessment){
            // is an assessment question
            this.isAssessmentQuestion = true;

            this.assessmentID = node.assessment;
            var assessmentArr = this.assessmentID.split('-');
            //console.log('ComponentMcqtawa::', assessmentArr[0].toLowerCase());
            this.assessmentController = window[assessmentArr[0].toLowerCase()+'Assessment'];
            this.questionNum = parseInt(assessmentArr[1])-1; // -1 for 0 indexing
            if (!isNaN(this.questionNum)){
                this.questionData = this.assessmentController.getQuestionXMLData(this.questionNum);
            }

            this.showMarking = this.assessmentController.showMarkingEnabled();
            this.showAnswers = this.assessmentController.showAnswersEnabled();
        }

        this.interactionID = (this.isAssessmentQuestion) ? this.assessmentID : this.slideID+'c'+this.num;

        // save data
        this.saveData = this.componentController.getSaveData(this.num);
        //console.log('Component', JSON.stringify(this.saveData));

        this.customData = (this.dataNode.customData) ? this.dataNode.customData : 'questions.'+this.slideID;
        this.customDataObj = customDataController.getCustomData(this.customData);
    },
    
    /**
     * @function
     * @description  This function adds the component HTML
     */
    getHTML: function(){
        //console.log('dataNode', dataNode)
        if (courseConfig.dataFormat == 'xml'){
            // send to recurseXML for XML
            return this.componentController.generateComponents(this.dataNode);//htmlUtils.recurseXML(this.dataNode);
        } else {
            return htmlUtils.recurseData(this.dataNode);
        }
    },

    /**
     * @function
     * @description  This function sets up the component
     */
    setupComponent: function(){
        //console.log('Component.prototype.setupComponent', this.id);
        this.$component = $('#'+this.id);
    },
    
    /**
     * @function
     * @description  This function fires when sets up complete
     */
    setupComplete: function(){
        
        this.isSetup = true;
    },
    
    /**
     * @public
     * @function 
     * @description  This function fired when component is displayed
     */
    componentDisplayed: function(){
        this.timeDisplayed = utils.getCurrentTime();
    },
    
    /**
     * @function unload
     * @description  This function is called on unload
     */
    unload: function(){
    },    
    
    /**
     * @function resize
     * @description  This function is called on browser resize
     */
    resize: function(){
    },

    /**
     * @function breakpointChanged 
     * @description  This function is called on device breakpoint changed
     */
    breakpointChanged: function(){
    },
    
    /**
     * @function resume
     * @description  This function is called on resume
     */
    resume: function(){
    }
}