/**
 * @fileoverview This is used to create a Flipcard component.
 * @class
 * @augments Component
 * @author LIVELOGIC
 * @version 1.0
 */
function ComponentFlipcard(){
}

ComponentFlipcard.prototype = Object.create(Component.prototype);

/**
 * @function
 * @description  This function initialiases the component
 */
ComponentFlipcard.prototype.init = function(_componentController, node, _id, _num){
    Component.prototype.init.call(this, _componentController, node, _id, _num);

    this.managesCompletion = true;
}


/**
 * @function
 * @description  This function adds the component HTML
 */
ComponentFlipcard.prototype.getHTML = function(){
    var self = this;   

    var isTitleSROnly = false;

    var hasFrontComponents = self.dataNode.front[0].components && self.dataNode.front[0].components[0]._children;
    var hasBackComponents = self.dataNode.front[0].components && self.dataNode.front[0].components[0]._children;
    
    var htmlStr = '';
    
    htmlStr += '<div class="flip-card" role="button" tabindex="0" aria-controls="'+self.id+'-back" aria-expanded="false">';
        var styleStr = htmlUtils.getStyles(self.dataNode.front[0]);
        if (styleStr.indexOf('background')){
            
        }
        htmlStr += '<div class="flip-card-inner'+(styleStr.indexOf('background') != -1 ? ' bkgImage' : '')+'">';
            htmlStr += '<div class="flip-card-front" style="'+styleStr+'">';
                htmlStr += '<div class="flip-card-content">';
                    //htmlStr += '<div class="optionLetter">'+htmlUtils.alphabetArray[i].toUpperCase()+'</div>';
                    
                    if (self.dataNode.front[0].title){
                        // lets check if the title has the sr-only class
                        isTitleSROnly = htmlUtils.getClasses(self.dataNode.front[0].title[0]).split(' ').indexOf('sr-only') != -1;
                    }
                    
                    if (hasFrontComponents){
                        htmlStr += '<div class="flip-card-content-components">';
                            htmlStr += this.componentController.generateComponents(self.dataNode.front[0].components[0]); 
                        htmlStr += '</div>';
                    }

                    // add sr-only class (screen reader only) if there are components on the front of the card
                    var srOnly = isTitleSROnly ? ' sr-only' : '';
                    htmlStr += '<div class="flipcard-title'+srOnly+'">'+htmlUtils.getHTMLText(self.dataNode.front[0].title[0])+'</div>';
                    if (self.dataNode.front[0].text){
                        htmlStr += '<div class="flipcard-text">'+htmlUtils.getHTMLText(self.dataNode.front[0].text[0])+'</div>';
                    }
                    htmlStr += '<div class="flip-icon">'+svgUtils.createSVG('plus')+'</div>';
                htmlStr += '</div>';
            htmlStr += '</div>';
            htmlStr += '<div id="'+self.id+'-back" class="flip-card-back" aria-hidden="true" aria-live="polite">';
                htmlStr += '<div class="flip-card-content">';
                    if (self.dataNode.back[0].title){
                        // back title overrides front title
                        htmlStr += '<h3 class="flipcard-title">'+htmlUtils.getHTMLText(self.dataNode.back[0].title[0])+'</h3>';
                    } else if (self.dataNode.front[0].title && !isTitleSROnly){
                        
                        htmlStr += '<h3 class="flipcard-title">'+htmlUtils.getHTMLText(self.dataNode.front[0].title[0])+'</h3>';
                    }
                    if (hasBackComponents){
                        htmlStr += this.componentController.generateComponents(self.dataNode.back[0].components[0]); 
                    }
                    if (self.dataNode.back[0].text){
                        htmlStr += '<div class="flipcard-text">'+htmlUtils.getHTMLText(self.dataNode.back[0].text[0])+'</div>';
                    }
                    htmlStr += '<div class="flip-icon">'+svgUtils.createSVG('minus')+'</div>';
                htmlStr += '</div>';
            htmlStr += '</div>';
        htmlStr += '</div>';
    htmlStr += '</div>';
    
    
//    htmlStr = '<div class="test-flipcard h">';
//        htmlStr += '<div class="front">';
//            htmlStr += 'This is the front side';
//        htmlStr += '</div>';
//        htmlStr += '<div class="back">';
//            htmlStr += 'This is the back side';
//        htmlStr += '</div>';
//    htmlStr += '</div>';
    
    return htmlStr;
}

/**
 * @function
 * @description  This function sets up the component
 */
ComponentFlipcard.prototype.setupComponent = function(){
    Component.prototype.setupComponent.call(this);

    var self = this; 
}

/**
 * @function
 * @description  This function sets up the component
 */
ComponentFlipcard.prototype.setupComplete = function(){
    Component.prototype.setupComplete.call(this);
    
    var self = this;   
    // self.$component.find('.flip-card').on('click',function(){
    self.$component.find('.flip-card').on('click keypress', utils.handleClickAndPress(function (e) {
        var $this = $(e.currentTarget);
        if ($this.hasClass('disabled')){
            return false;
        }
        $this.addClass('done');
        $this.toggleClass('flip');
        
        $this.attr('aria-expanded', ($this.hasClass('flip') ? 'true' : 'false'));

        self.$component.find('.flip-card-front').attr('aria-hidden', ($this.hasClass('flip') ? 'true' : 'false'));
        self.$component.find('.flip-card-back').attr('aria-hidden', ($this.hasClass('flip') ? 'false' : 'true'));
        // $this.blur();
        setTimeout(function(){
            $this.find('.flip-card-back').focus();
        }, 1000);
        
        // set component complete
        self.componentController.setComponentComplete(self);
    }));

    // if option contains any anchor tags for URLs, modals or popovers, prevent selection of option
    self.$component.find('.flip-card-content a').on('click', function(e){
        e.stopPropagation();
        //alert('a click')
    })
//    self.$component.find('.test-flipcard').on('click',function(){
//        $(this).addClass('done');
//        $(this).toggleClass('flip');
//        
//        // set component complete
//        //self.componentController.setComponentComplete(self);
//    })
}

/**
 * @public
 * @override 
 * @description  This function is called on unload
 */
ComponentFlipcard.prototype.unload = function(){
    var self = this;    
}

/**
 * @public
 * @override 
 * @description  This function fired when component is displayed
 */
ComponentFlipcard.prototype.componentDisplayed = function(){
    Component.prototype.componentDisplayed.call(this);

    var self = this;    
    
    // set component complete
    //self.componentController.setComponentComplete(self);
    
    
}
/**
 * @public
 * @override 
 * @description  This function is called on browser resize
 */
ComponentFlipcard.prototype.resize = function(){
    var self = this;    
    
    var $flipComponents = self.$component.closest('.slide').find('.component-flipcard');
    if ($flipComponents.last().attr('id') == self.id){
        var $flipcards = $flipComponents.find('.flip-card-front, .flip-card-back');
        var maxHeight = 0;
        $flipComponents.find('.flip-card').height('auto');
        $flipcards.height('auto');
        $flipcards.each(function(){
            maxHeight = Math.max(maxHeight, $(this).outerHeight());
        })
        $flipComponents.find('.flip-card').height(maxHeight);
        $flipcards.css('height', '100%');
    }
}
