function TemplateCUSTOM(){
    var self = this;
    var $slide;
    var $contentWrap;
    this.slideObj;
    
    this.init = init;
    this.slideDisplayed = slideDisplayed;
    
    /**
     * @function init
     * @description  This function initialise the template.
     */
	function init(_slideObj) {
        self.slideObj = _slideObj;
        $slide = self.slideObj.$slide;
        // reference all items on the page using $slide.find(....)
        $contentWrap = self.slideObj.$slide.find('.content-wrap .content-wrap-inner');
        
        addXMLData();
        addFunctionality();
        
        slideReady();
    }
    
    /**
     * @function addXMLData
     * @description  This function adds the copy and info from the XML
     */
    function addXMLData(){        
        var htmlStr = '';
//        htmlStr += self.slideObj.generateComponents(self.slideObj.data.components[0]);
        htmlStr += self.slideObj.generateComponents(self.slideObj.data);//.components[0]);
        $contentWrap.append(htmlStr);
    }
    
    
    /**
     * @function addEvents
     * @description  This function adds the page functionality
     */
    function addFunctionality(){
        
    }
    
    /**
     * @function slideReady
     * @description  This function is called when the slide is ready
     */
    function slideReady(){
        self.slideObj.slideReady();
    }
    
    /**
     * @function slideDisplayed
     * @description  This function is called when the slide is displayed
     */
    function slideDisplayed(){
        slideComplete();
    }
    
    /**
     * @function slideComplete
     * @description  This function is called when the slide is completed
     */
    function slideComplete(){
        self.slideObj.slideContentComplete();
    }
    
    /**
     * @function updateLayout
     * @description  This function is called on a browser resize
     */
    function updateLayout(){
        
    }
}