 /**
 * @fileoverview This is used to create a Group component.
 * @class
 * @author LIVELOGIC
 * @version 1.0
 */
function ComponentInstruction(){
    var currentInstruction = 0;
}

ComponentInstruction.prototype = Object.create(Component.prototype);

/**
 * @function
 * @description  This function adds the component HTML
 */
ComponentInstruction.prototype.init = function(_componentController, node, _id, _num){
    Component.prototype.init.call(this, _componentController, node, _id, _num);
    
    this.manual = this.dataNode.manual ? true : false; 

    this.hasSubmenuCompleteInstruction = false;
}

/**
 * @function
 * @description  This function adds the component HTML
 */
ComponentInstruction.prototype.getHTML = function(){
    var self = this;
    
    var htmlStr = '';

    // var instructionText = this.dataNode.text;
    htmlStr += '<div class="instructions">';
    
        for (var i = 0; i < self.dataNode.text.length; i++){
            var instruction = self.dataNode.text[i].Text ? htmlUtils.getHTMLText(self.dataNode.text[i]) : '';

            // check if we're a submenuitem - if so let's change instruction text
            if (self.dataNode.text[i].submenucomplete){
                self.hasSubmenuCompleteInstruction = true;
            }

            htmlStr += '<p class="instruction-text'+(self.dataNode.text[i].submenucomplete ? ' instruction-submenucomplete' : '')+'">'+instruction+'</p>';
        }
     
    
    htmlStr += '</div>';
    return htmlStr;
}

/**
 * @function
 * @description  This function adds the component HTML
 */
ComponentInstruction.prototype.changeInstruction = function(num){
    var self = this;
    
    
    console.log('ComponentInstruction.prototype.changeInstruction', num);
    
    var $texts = self.$component.find('.instruction-text').not('.rolehidden');

    if (self.hasSubmenuCompleteInstruction){
        var submenu = courseData.getSubmenu();
        console.log(submenu);
        if (!submenu.complete){
            $texts = $texts.not('.instruction-submenucomplete');
        }
    }

    num = Math.min(num, $texts.length-1);
    
    // hide if no text to show
    if ($texts.eq(num).text() == ''){
        // console.log('ComponentInstruction.prototype.changeInstruction hide', self.num);
        // self.$component.slideUp();
    } else {
        // console.trace('ComponentInstruction.prototype.changeInstruction show', self.num);
        // self.$component.slideDown();
    }
    if ($texts.length > 1 && num != self.currentInstruction){
        $texts.slideUp();
        $texts.eq(num).slideDown();
        self.currentInstruction = num;
    }
    
}