 /**
 * @fileoverview This is used to create an ComponentMcqtawa component.
 * @class
 * @author LIVELOGIC
 * @version 1.0
 */
function ComponentMcqtawa(){
}

ComponentMcqtawa.prototype = Object.create(Component.prototype);

/**
 * @function
 * @description  This function adds the component HTML
 */
ComponentMcqtawa.prototype.init = function(_componentController, node, _id, _num){
    Component.prototype.init.call(this, _componentController, node, _id, _num);

    var self = this;

    this.managesCompletion = true;
    
    this.answerArray = [];
    this.userSelectionArray = [];
    this.isCorrectArray = [];
    
    this.dataLabelArray = [];
    
    this.totalAttempts = (node.attempts) ? parseInt(node.attempts) : 1;
    this.numAttempts = 0;

    // this.numToSelect = (node.numToSelect) ? parseInt(node.numToSelect) : 1; // default select 1

    if (self.isAssessmentQuestion && self.dataNode.compare){
        // assessment to compare result against
        // self.compare = self.dataNode.compare ? window[self.dataNode.compare.toLowerCase()+'Assessment'] : false;
        self.compareAssessment = self.dataNode.compare ? window[self.dataNode.compare.toLowerCase()+'Assessment'] : false;
        if (self.compareAssessment){
            // console.log('ComponentMcqtawa compareQuestion', self.questionNum);
            self.compareQuestion = self.compareAssessment.getQuestionDataForNum(self.assessmentController.getQuestionData(self.questionNum).q);
            // console.log('ComponentMcqtawa compareQuestion', self.compareQuestion);
        }
    }
    
}

/**
 * @function 
 * @description  This function adds the component HTML
 */
ComponentMcqtawa.prototype.getHTML = function(){
    var self = this;
    
    var submit = this.dataNode.submit[0].text[0].Text;
    var secondary = this.dataNode.secondary[0].text[0].Text;
    var htmlStr = '';
    
    htmlStr += '<form class="form-inline">';
        htmlStr += '<fieldset>';
            htmlStr += '<legend id="'+self.id+'-legend">';
                htmlStr += '<div class="questionPanel">';
                if (this.dataNode.assessment){
                    htmlStr += '<div class="questionNum moduleColor">'+(self.questionNum+1)+'</div>';
                }
                    htmlStr += '<div class="questionInfo">';
                        htmlStr += '<div id="'+self.id+'-question" class="question-text">';
                            htmlStr += htmlUtils.getHTMLText(this.dataNode.question[0]);
                        htmlStr += '</div>';
                        if (secondary){
                        htmlStr += '<div class="secondary-instruction anim fadeIn">'+secondary+'</div>'; 
                        }
                    htmlStr += '</div>';
                htmlStr += '</div>';
            htmlStr += '</legend>';
    
    if (self.dataNode.instruction){
        htmlStr += self.componentController.generateComponents(self.dataNode.instruction[0]);
    }
    
    var options = this.dataNode.options[0].option;
    var colClasses = responsiveUtils.calculateChildBootstrapColumns(this.dataNode.options[0]);

    
    var numCorrect = 0;
    for (var i = 0; i < options.length; i++){  
        if (options[i].correct == 'NA'){
            self.answerArray[i] = 'NA';
        } else {
            self.answerArray[i] = (options[i].correct === 'true') ? 1 : 0;
            numCorrect += self.answerArray[i];
        }
    }

    // use lodash _.without to get number of correct items
    self.numCorrectAnswers =  _.without(self.answerArray, 0).length;
    
            htmlStr += '<div class="options'+(self.numCorrectAnswers > 1 ? ' mcq' : ' scq' )+(colClasses != '' ? ' options-cols' : '' )+'"'+(self.numCorrectAnswers == 1 ? ' role="radiogroup"' + 'aria-labelledby="'+self.id+'-legend"' : '' )+'>';
    
            if (colClasses != ''){
                htmlStr += '<div class="row flex-row">';
            }   


    
    for (var i = 0; i < options.length; i++){  
        self.dataLabelArray[i] = (options[i].dataLabel) ? options[i].dataLabel : i;
        
        var isCorrect = (options[i].correct === 'true');
        //var text = options[i].text[0].Text;
        // var correctClass = ' correct';
        // if (!isCorrect && options[i].correct != 'NA'){
        //     correctClass = ' incorrect';
        // }
        // if (self.dataNode.options[0].option[i].exclusive){
        //     correctClass = ' exclusive';
        // }

        
        if (colClasses != ''){
        htmlStr += '<div class="'+colClasses+'" >';
        }
        htmlStr += '<div class="option'+(self.dataNode.options[0].option[i].exclusive ? ' exclusive' : '')+' anim fadeInRight" role="'+(self.numCorrectAnswers == 1 ? 'radio' : 'checkbox' )+'" aria-checked="false" aria-labelledby="'+self.id+'-option-'+i+'" tabindex="0" data-index="'+i+'">';
            htmlStr += '<div class="optionInner">';
                if (options[i].src){
                    var path = utils.getImagePath(options[i].src);
                    htmlStr += '<div class="optionImg"><img class="img-responsive" src="'+path+'"></div>';
                }
                htmlStr += '<div class="checkboxIcon"><span class="tickCross">'+(self.answerArray[i] != 'NA' ? svgUtils.createSVG('tickcross') : '')+'</span></div>';
                htmlStr += '<div id="'+self.id+'-option-'+i+'" class="btnText">'+htmlUtils.getHTMLText(this.dataNode.options[0].option[i].text[0])+'</div>';
    //            htmlStr += '<span class="tickCross">'+svgUtils.createSVG('tickcross')+'</span>';
            htmlStr += '</div>';
            if (self.compareQuestion){
            htmlStr += '<div class="option-compare-selection">';
                htmlStr += '<div>'+courseData.data.assessment.labels[0].compare[0].Text+'</div>';
            htmlStr += '</div>';
            }
        htmlStr += '</div>'; // end option
        if (colClasses != ''){
        htmlStr += '</div>'; // end col
        }
    }

    
    
    if (colClasses != ''){
        htmlStr += '</div>'; //end row
        }
    htmlStr += '</div>'; // end options
    
            htmlStr += '<button class="btn ok pull-right disabled anim fadeInUp" aria-disabled="true">'+submit+'</button>';

        htmlStr += '</fieldset>';
    htmlStr += '</form>';
    
    if (self.dataNode.options[0].numToSelect != undefined){
        self.numToSelect = parseInt(self.dataNode.options[0].numToSelect);
    } else {
        self.numToSelect = (numCorrect > 1) ? -1 : 1; // -1 is select as many as you like
    }

    if (self.isAssessmentQuestion){
        self.assessmentController.initQuestion(self.questionNum, self.answerArray);
    }
    
    return htmlStr;//'[MCQTAWA HERE]';
} 

/**
 * @function
 * @description  This function sets up the component
 */
ComponentMcqtawa.prototype.setupComponent = function(){
    Component.prototype.setupComponent.call(this);
    
    var self = this;   
}

/**
 * @function
 * @description  This function fires when sets up complete
 */
ComponentMcqtawa.prototype.setupComplete = function(){
    Component.prototype.setupComplete.call(this);
    
    var self = this;   

    // use feedback controller to init feedback
    self.$feedback = self.$component.closest('.slide').find('.feedback');
    feedbackController.initFeedback(self.$component, self.$feedback);
}

/**
 * @public
 * @override 
 * @description  This function is called on unload
 */
ComponentMcqtawa.prototype.unload = function(){
    var self = this;    
}

/**
 * @public
 * @override 
 * @description  This function fired when component is displayed
 */
ComponentMcqtawa.prototype.componentDisplayed = function(){
    Component.prototype.componentDisplayed.call(this);

    var self = this;
    //console.trace('ComponentMcqtawa.prototype.componentDisplayed')
    var $options = self.$component.find('.option');

    
    self.checkDone();
    
    $options.on('click keypress', utils.handleClickAndPress(function (e) {
        var $this = $(e.currentTarget);
        if ($this.hasClass('disabled')){
            return false;
        }
        
        // if ($options.filter('.correct').length == 1 || (self.numToSelect == 1 && $options.filter('.correct').length > 1)){
        
        if (self.numCorrectAnswers == 1 || (self.numToSelect == 1 && self.numCorrectAnswers > 1)){
            // $options.not($this).removeClass('selected').attr('aria-pressed', 'false');
            $options.not($this).removeClass('selected').attr('aria-checked', 'false');
        }
        $this.toggleClass('selected');
        if (self.numToSelect > 0 && $options.filter('.selected').length > self.numToSelect){
            // if there ismore than the set number to select, don't allow this to be selected
            $this.removeClass('selected');
        }
        if ($this.hasClass('selected')){
            // $this.attr('aria-pressed', 'true');
            $this.attr('aria-checked', 'true');
        }
        if ($this.hasClass('exclusive')){
            // is this an exclusive option - deselects all others
            $options.not(this).removeClass('selected');
        } else {
            // deselect the exclusive option
            $options.filter('.exclusive').removeClass('selected');
        }
        
        self.checkDone();
    }));

    // if option contains any anchor tags for URLs, modals or popovers, prevent selection of option
    $options.find('.btnText a').on('click', function(e){
        e.stopPropagation();
        //alert('a click')
    });
    
    // prevent enter submitting form
    self.$component.find('input').on('keydown', function(event) {
        return event.key != 'Enter';
    });

    self.$component.find('.btn.ok').on('click keypress', utils.handleClickAndPress(function (e) {
        var $this = $(e.currentTarget);
        
        // as now in a form we need to stop the form submitting
        e.preventDefault();

        if ($this.hasClass('disabled')){
            return false;
        }

        //self.$component.find('.secondary-instruction').hide();
        $this.hide();
        
        self.numAttempts++;
        
        self.numCorrectSelected = 0;
        self.numIncorrectSelected = 0;
        $options.each(function(){
            var optionIndex = $(this).data('index');
            self.userSelectionArray[optionIndex] = $(this).hasClass('selected') ? 1 : 0;
            if (self.userSelectionArray[optionIndex] == 1){
                if (self.answerArray[optionIndex] == 1){
                    self.numCorrectSelected ++;
                } else {
                    self.numIncorrectSelected ++;
                }
                // feedback shown for option selection - requires class .feedbackOption1, .feedbackOption2
                self.$feedback.find('.feedbackOption'+optionIndex).show();
                if (self.dataNode.options[0].option[optionIndex].feedback){
                    // any feedback referenced by xml - requires class .feedback1, .feedback2 
                    self.$feedback.find('.feedback'+self.dataNode.options[0].option[optionIndex].feedback).show();
                }
            }
        });
        
        var $optionsThisAttempt = $options.not('.disabled');
        
        // disable options and set to tabindex -1 so not visually focusable
        $options.disable().attr('tabindex', -1);
//        for (var i = 0; i < self.answerArray.length; i++){
//            // create an array saying if user was correct for each option
//            self.isCorrectArray[i] = (self.answerArray[i] == self.userSelectionArray[i]) ? 1 : 0;
//        } 
        
        if (courseConfig.googleAnalytics.enabled){
            // Google analytics - track user answers
            gtag('event', 'engagement', {
              'event_category' : self.slideID,
              'event_label' : self.userSelectionArray.toString()
            });
        }
        
                //console.log (self.$component.find('.tfCheckbox.selected.correct').length, self.$component.find('.option').length);
        var isDone = false;
        var isCorrect = false;
        if ((self.numCorrectSelected === self.numCorrectAnswers 
           || self.numCorrectSelected === self.numToSelect)
           && self.numIncorrectSelected == 0){
        // if ((self.numCorrectSelected === self.numCorrectAnswers 
        //    || self.numCorrectSelected === self.numToSelect)
        //    && self.numIncorrectSelected == 0){
            isCorrect = true;
            isDone = true;
            self.$feedback.find('.feedbackDone').show();
            
            self.$feedback.removeClass('incorrect partlycorrect').addClass('correct');
        } else {
            
            // allow retry?
            if (self.numAttempts < self.totalAttempts){
                self.$component.addClass('attempted');
                self.$feedback.find('.feedbackRetry').show();
                
            } else {
                self.$feedback.find('.feedbackDone').show();
                isDone = true;
            }
            
            if (self.$feedback.find('.feedbackPartlyCorrect').length && self.numCorrectSelected > 0){
                self.$feedback.removeClass('incorrect').addClass('partlycorrect');
            } else {
                self.$feedback.removeClass('partlycorrect').addClass('incorrect');
            }
        }
        
        // use feedback controller to show feedback
        feedbackController.showFeedback(self.$component, self.$feedback);

        self.$component.find('.component-instruction').slideUp();

        if (!self.showMarking){
            self.$component.addClass('hideOptionMarking');
        }
        if (!self.showAnswers){
            self.$component.addClass('hideOptionAnswers');
        }
        
        if (self.isAssessmentQuestion){
            
            self.assessmentController.storeAnswers(self.questionNum, self.userSelectionArray);//, self.isCorrectArray);
            
            htmlUtils.updateTextVariables();
        } 
            
        // let's save the interaction
        storageController.setInteraction({
            id: self.interactionID, 
            type: 'choice', 
            question: self.$component.find('.questionInfo').text(),
            response: self.userSelectionArray, 
            answers: self.answerArray, 
            correct: (self.answerArray[0] != 'NA') ? isCorrect : 'neutral',
            timestamp: self.timeDisplayed
        });
        
        // automatically store on custom data questions if not set - not for assessments
        if (!self.isAssessmentQuestion){
            // store answer in suspend data
            if (self.answerArray[0] != 'NA'){
                if (self.customDataObj == undefined){
                    self.customDataObj = {
                        answers: self.answerArray,
                        attempts: [],
                        correct: 0
                    }
                }
                if (self.customDataObj.correct == 0){
                    console.log('attempts', self.customDataObj.attempts)
                    self.customDataObj.attempts.push(self.userSelectionArray.slice());
                    self.customDataObj.correct = (self.answerArray[0] != 'NA') ? utils.booleanToInt(isCorrect) : 'neutral';
                    customDataController.setCustomData(self.customData, self.customDataObj);
                }

            } else {
                // save the array of answers if no correct/incorrect
                var dataArray = [];
                $options.filter('.selected').each(function(index){
                    var optionIndex = $(this).data('index');
                    var dataStr = optionIndex != self.dataLabelArray[optionIndex] ? optionIndex+'-'+self.dataLabelArray[optionIndex] : optionIndex;
                    dataArray.push(dataStr);
                });
                customDataController.setCustomData(self.customData, dataArray);
            }
        }

        
        
        if (isDone){
            self.$component.addClass('done');

            if (self.compareQuestion){
                if (self.compareQuestion.attempts.length){
                    var compareAttemptsArray = self.compareQuestion.attempts[self.compareQuestion.attempts.length-1];
                    if (compareAttemptsArray.length){
                        console.log('compare', compareAttemptsArray);
                        $options.each(function(){
                            var optionIndex = $(this).data('index');
                            if (compareAttemptsArray[optionIndex] == 1){
                                $(this).addClass('previouslySelected').find('.option-compare-selection').slideDown();
                            }
                        });
                        // choose feedback to display
                        if (self.compareQuestion.correct == 1 && isCorrect){
                            self.$feedback.find('.feedbackCompareCorrectCorrect').show();
                        }
                        if (self.compareQuestion.correct == 1 && !isCorrect){
                            self.$feedback.find('.feedbackCompareCorrectIncorrect').show();
                        } 
                        if (self.compareQuestion.correct == 0 && isCorrect){
                            self.$feedback.find('.feedbackCompareIncorrectCorrect').show();
                        }
                        if (self.compareQuestion.correct == 0 && !isCorrect){
                            self.$feedback.find('.feedbackCompareIncorrectIncorrect').show();
                        }
                    }
                }
            }
            if (self.answerArray[0] != 'NA'){
                $optionsThisAttempt.each(function(){
                    var $this = $(this);
                    var optionIndex = $this.data('index');
                    
                    var timeline = svgUtils.animateSVG('tickcross', $this);
                    timeline.restart();
                    //timeline.stop();
                    $this.addClass(self.answerArray[optionIndex] === 1 ? 'correct' : 'incorrect');

                    var correctLabel = '';
                    if ($this.hasClass('selected')){
                        timeline.play();
                        correctLabel = courseData.getAccessibleLabel(self.answerArray[optionIndex] === 1 ? 'correct' : 'incorrect');
                    } else {
                        if (self.answerArray[optionIndex] === 1){
                            correctLabel = courseData.getAccessibleLabel('correctMissed');
                        }
                        // $(this).addClass('correct');
                        timeline.play();
                        timeline.delay(2);
                        // $this.attr('aria-checked', 'true');
                        
                    } 
                    if (correctLabel != ''){
                        $this.find('.btnText span').prepend('<span class="sr-only">'+correctLabel+'&nbsp;</span>');
                    }
                });
            }
            self.componentController.setComponentComplete(self);
        } else {
            if (self.answerArray[0] != 'NA'){
                $optionsThisAttempt.filter('.selected').each(function(){
                    var optionIndex = $(this).data('index');
                    $(this).addClass(self.answerArray[optionIndex] === 1 ? 'correct' : 'incorrect');
                    var timeline = svgUtils.animateSVG('tickcross', $(this));
                });
            }
        }
    }));
}

/**
 * @public
 * @override 
 * @description  This function fired on checkDone
 */
ComponentMcqtawa.prototype.checkDone = function(){
    var self = this;

    var $options = self.$component.find('.option');

    if (self.numToSelect == 0 
    || (self.numToSelect == -1 && $options.filter('.selected').length > 0) 
    || (self.numToSelect > 0 && $options.filter('.selected').length == self.numToSelect)){
        // activate ok
        self.$component.find('.btn.ok').enable();
    } else {
        self.$component.find('.btn.ok').disable();
    }
}

/**
 * @public
 * @override 
 * @description  This function fired on retry
 */
ComponentMcqtawa.prototype.retry = function(){
    var self = this;
    //console.log('ComponentMcqtawa retry');
    
    self.$component.find('.btn.ok').show();
//    var $tfoptions = self.$component.find('.tfoption');
//    $tfoptions.enable();
    //self.$component.removeClass('attempted');
    
//    var $options = self.$component.find('.option').not('.selected.correct').removeClass('selected disabled');
    self.$component.find('.option.incorrect.selected').addClass('attempted').removeClass('selected');
    // enable options not attempted and set to tabindex 0 so visually focusable
    var $options = self.$component.find('.option').not('.attempted').enable().attr('tabindex', 0);
//    $options.each(function(index){
//        $(this).removeClass('selected');
//        $(this).find('.tfoption').removeClass('selected disabled');
//    });

	self.checkDone();

    feedbackController.hideFeedback(self.$component, self.$feedback);
    
    self.$component.closest('.slide').find('.component-instruction').not(self.$feedback.find('.component-instruction')).slideDown();
}

/**
 * @public
 * @override 
 * @description  This function fired on resize
 */
ComponentMcqtawa.prototype.resize = function(){
    var self = this;

    //resize when we're using columns so heights match
    utils.matchElementHeights(self.$component.find('.options.options-cols .option .optionInner'), ['xs']);
}

/**
 * @public
 * @override 
 * @description  This function is called on device breakpoint changed
 */
ComponentMcqtawa.prototype.breakpointChanged = function(){
    var self = this;
 }
