/**
 * @fileoverview This is used to create a Media component.
 * @class
 * @augments Component
 * @author LIVELOGIC
 * @version 1.0
 */
function ComponentMedia(){
}

ComponentMedia.prototype = Object.create(Component.prototype);

/**
 * @function
 * @description  This function initialiases the component
 */
 ComponentMedia.prototype.init = function(_componentController, node, _id, _num){
    Component.prototype.init.call(this, _componentController, node, _id, _num);

    var self = this;

    this.managesCompletion = true;
    this.nextOnComplete = (this.dataNode.nextOnComplete == "true");
    this.nextSlideOnComplete = (this.dataNode.nextSlideOnComplete == "true");

    this.fullscreen = (self.dataNode.fullscreen == 'true');
    this.autoplay = (self.dataNode.autoplay == 'true');
    this.controls = !(self.dataNode.controls == 'false');
    this.transcript = !(self.dataNode.transcript == 'false');

    var srcExt = utils.getUrlExtension(this.dataNode.src);
    this.isVideo = (srcExt == 'mp4' || srcExt == 'webm' || srcExt == 'ogv');
    this.mediaTypeString = this.isVideo ? 'Media:Video' : 'Media:Audio';
    this.showTranscriptState = false;
    
    this.isVideoInitialised = false;

    // can the transcript button be part of the control bar? defaul is false;
    this.controlTranscript = (self.dataNode.controlTranscript == 'true');

    this.showTranscript = function(){
        let $transcriptReveal = self.$component.find('.transcript-reveal');
        $transcriptReveal.removeClass('collapsed').slideDown();
        self.$component.find('.transcript-toggle-btn').attr('aria-expanded', true);
        self.$component.find('.vjs-subtitles-button').attr('aria-expanded', true);
        self.$component.find('.transcript-toggle-btn .transcript-icon-down').addClass('hide-transcript-icon');
        self.$component.find('.transcript-toggle-btn .transcript-icon-up').removeClass('hide-transcript-icon');
        self.showTranscriptState = true;
    }

    this.hideTranscript = function(){
        let $transcriptReveal = self.$component.find('.transcript-reveal');
        $transcriptReveal.addClass('collapsed').slideUp();
        self.$component.find('.transcript-toggle-btn').attr('aria-expanded', false); 
        self.$component.find('.vjs-subtitles-button').attr('aria-expanded', false);  
        self.$component.find('.transcript-toggle-btn .transcript-icon-down').removeClass('hide-transcript-icon');
        self.$component.find('.transcript-toggle-btn .transcript-icon-up').addClass('hide-transcript-icon');
        self.showTranscriptState = false;
    }
    this.handleShowTranscriptToggle = function(){
        if (self.showTranscriptState){
            self.hideTranscript();
        } else {
            self.showTranscript();
        }
    }
    this.getCaptions = function(){
        var displaySubtitles = true;
        if(storageController.getCaptions() === null){
            displaySubtitles = (self.dataNode.subtitles === "true") ? true: false;
        } else {
            displaySubtitles = storageController.getCaptions();
        }
        return displaySubtitles;
    }
}

