/**
 * @function
 * @description  This function sets up the component
 */
ComponentMedia.prototype.setupComponent = function(){
    Component.prototype.setupComponent.call(this);
    
    var self = this;   
}

/**
 * @function
 * @description  This function fires when sets up complete
 */
ComponentMedia.prototype.setupComplete = function(){
    Component.prototype.setupComplete.call(this);
    
    var self = this;

    if (self.fullscreen){
        self.$component.addClass('fullscreen');
    }
    if (self.nextOnComplete){
        self.$component.addClass('nextOnComplete');
    }
    if (self.nextSlideOnComplete){
        self.$component.addClass('nextSlideOnComplete');
    }
    
    if (!self.controlTranscript){
        // if no transcript button on player control bar then add click event to separate transcript button
        self.$component.find('.transcript-toggle-btn').on('click', utils.handleClickAndPress(function (e) {
            if (e.type === 'click' || (e.type === 'keypress' && e.keyCode === 13)) {
                e.preventDefault();
                var $this = $(e.currentTarget);
                self.handleShowTranscriptToggle();
                self.componentController.setComponentComplete(self);
            }
        }));
    }
}
    
/**
 * @public
 * @override 
 * @description  This function fired when component is displayed
 */
ComponentMedia.prototype.componentDisplayed = function(){
    Component.prototype.componentDisplayed.call(this);
    var self = this;

    // console.log(self.mediaTypeString, 'componentDisplayed');  
    
    var vtt = this.dataNode.vtt;
    // var displaySubtitles = (this.dataNode.subtitles === "true") ? true: storageController.getCaptions();
    // var displaySubtitles = (this.dataNode.subtitles === "true") ? true: false;

    var displaySubtitles = this.getCaptions();
    
    // console.log("storageController.getCaptions()", storageController.getCaptions());
    
    if (vtt){
        if (vtt.indexOf('http') == -1){
            vtt = utils.getURL(langPath+'xml/subtitles/'+vtt);
        }
    }

    var mediaplayer = null;
    if(self.isVideo){
        // using the video player
        mediaplayer = videojs(self.id+'-video', {
            fluid: (!self.fullscreen ? true : false),
            fill: (self.fullscreen ? true : false),
            language: languageConfig.languages[currentLanguage].iana_code,
            muted: audioController ? audioController.isMuted : false,
            // playbackRates: [1, 1.5, 2],
    //        fill: true
    //        responsive: 
            textTrackSettings: false // Make the text track settings dialog not initialize.
        });
    } else {
        // using the audio player
        mediaplayer = videojs(self.id+'-audio', {
            fluid: (self.dataNode.poster ? true : false),
            fill: (self.fullscreen ? true : false),
            language: languageConfig.languages[currentLanguage].iana_code,
            muted: audioController ? audioController.isMuted : false,
            textTrackSettings: false // Make the text track settings dialog not initialize.
        });
        
        if(!displaySubtitles){
            self.$component.find('.video-js').addClass("hide-video-area");
        }
    }

    self.isVideoInitialised = true;
    

    if (vtt && self.transcript || self.transcript){
        if (self.controlTranscript){
            var button = videojs.getComponent('Button');
            var transcriptButton = videojs.getComponent('transcriptButton-'+self.componentID);
            // console.log('transcriptButton', transcriptButton);
            if (!transcriptButton){
                var transcriptButton = videojs.extend(button, {
                    constructor: function() {
                        button.apply(this, arguments);
                        this.controlText(courseData.getBtnText('Transcript'));
                        this.addClass('vjs-subtitles-button');
                        var $this = $(this);
                        $this.attr('aria-expanded', false);
                        $this.attr('aria-controls', self.id+'-transcript-reveal');
                    },
                    handleClick: function() {
                        // Access the player element
                        var playerElement = this.player().el();
                        var $component = $(playerElement).closest('.component');
                        // console.log(playerElement);
                        console.log('transcriptButton handleClick');
                        
                        var componentController = contentController.getComponentControllerForElement($component);
                        var component = componentController.getComponentObject($component);
                        // this.player().dispose();
                        component.handleShowTranscriptToggle();
                        // set component complete
                        componentController.setComponentComplete(component);

                    }
                });
                videojs.registerComponent('transcriptButton-'+self.componentID, transcriptButton);
            }
            
            mediaplayer.getChild('controlBar').addChild('transcriptButton-'+self.componentID, {});
        }
    }

    // this is called when the video is ready
    mediaplayer.on('ready', function() {
        // console.log(self.mediaTypeString, 'ready', mediaplayer);     
        
        self.isReady = true;
        
        self.$component.find('.vjs-big-play-button .vjs-icon-placeholder').html(svgUtils.createSVG('play'));

        setTimeout(function(){
            // hook into subitles button
            // console.log('video ready', self.$component.find('.vjs-subtitles-menu-item'));
            self.$component.find('.vjs-subs-caps-button .vjs-menu-item').on('click', function(){
                // console.log('.vjs-subtitles-menu-item CLICK', mediaplayer.textTracks()[0].mode);
                displaySubtitles = mediaplayer.textTracks()[0].mode == 'showing' ? true : false;
                if(displaySubtitles && !self.dataNode.poster && !self.isVideo){
                    self.$component.find('.video-js').removeClass("hide-video-area");
                } else {
                    self.$component.find('.video-js').addClass("hide-video-area");
                }
                storageController.setCaptions(displaySubtitles);
            });
        }, 500);

        if (vtt){
            // only create transcript if we have a vtt file and we don't have a transcript node
            if (self.transcript && self.dataNode.transcript == undefined){
                // auto generate transcript from content of VTT
                // console.log(vtt);
                var endOfSentenceChars = ['.','?','!',':'];
                $.get(vtt, function(response) {
                    var cueTexts = response.split(/[\n\r]/g).filter(function(entry){
                        return entry && isNaN(entry) && !(entry.match(/WEBVTT/) || entry.match((/(\d{2}:){1,2}\d{2}\.\d{3}/)));
                    });
                    var htmlStr = '';
                    var continueSentence = false;
                    for (var i = 0; i < cueTexts.length; i++) {
                        var vttStr = cueTexts[i].trim().replace(/<(\w+)\.(\w+)>(.*)<\/\1>/, "<span class=\"$2\">$3<\/span>");
                        // console.log(vttStr);
                        if (!continueSentence){
                            htmlStr += '<p>';
                        }
                        htmlStr += vttStr;
                        if (endOfSentenceChars.indexOf(vttStr.charAt(vttStr.length-1)) != -1 || i == cueTexts.length-1){
                            // htmlStr += '<p>'+vttStr+'</p>';
                            continueSentence = false;
                            htmlStr += '</p>';
                        } else {
                            continueSentence = true;
                            htmlStr += ' '; // add a space
                        }
                        
                    }
                    // self.$component.find('.accordion-reveal').html(htmlStr);
                    self.$component.find('.transcript-text').html(htmlStr);
                });
            }
            // add track subtitleslanguageConfig.languages[currentLanguage].code
            var trackEl = mediaplayer.addRemoteTextTrack({label:languageConfig.languages[currentLanguage].language, srclang:languageConfig.languages[currentLanguage].iana_code, src: vtt}, false);
            trackEl.addEventListener('load', function() {
                //console.log(mediaplayer.textTracks())
                if (displaySubtitles){
                    var track = mediaplayer.textTracks()[0].mode = 'showing';
                }
                
            //    var htmlStr = '';
            //    for (var i = 0; i < track.cues.length; i++) {
            //        htmlStr += '<p>'+track.cues[i].text+'</p>';
            //    }

            //    self.$component.find('.accordion-reveal').html(htmlStr);
            
            });
        }
    });  
    
    var eventID = 'Page '+navigationController.currentPage+' Slide '+self.componentController.slideObj.slideNum+' '+ self.dataNode.src+' '+(self.dataNode.title ? self.dataNode.title[0].Text : '');
    
    mediaplayer.on('play', function() {
        console.log(self.mediaTypeString, 'play');
        
        // check any other playing videos - pause if playing
        $('.component-media, .component-video, .component-audio').not(self.$component).each(function(){

            var componentController = contentController.getComponentControllerForElement($(this));
            componentController.getComponentObject($(this)).pause();
            // contentController.slideArray[$(this).closest('.slide').data('slide')].componentController.componentObjectsArray[$(this).data('componentindex')].pause();
        });
        
        displaySubtitles = self.getCaptions();
        if (displaySubtitles){
            mediaplayer.textTracks()[0].mode = 'showing';
            if(!self.isVideo){
            	self.$component.find('.video-js').removeClass("hide-video-area");
            }
        } else {
            mediaplayer.textTracks()[0].mode = 'hidden';
            self.$component.find('.video-js').addClass("hide-video-area");
        }
        
        if (courseConfig.googleAnalytics.enabled){
            gtag('event', 'Play', {
              'event_category' : 'Video',
              'event_label' : eventID
            });
        }
        storageController.setObjective('Video-'+eventID, 'incomplete', 'Video started');
    });
    
    mediaplayer.on('ended', function() {
        console.log(self.mediaTypeString,'complete')
        if (courseConfig.googleAnalytics.enabled){
            gtag('event', 'Complete', {
              'event_category' : self.isVideo ? 'Video' : 'Audio',
              'event_label' : eventID
            });
        }
        storageController.setObjective(self.mediaTypeString+'-'+eventID, 'completed', self.mediaTypeString+' complete');
		
		// fire event
        self.$component.trigger(courseEvent.VIDEO_COMPLETE);
        
        // set component complete
        // self.componentController.setComponentComplete(self);

        self.videoComplete();
    });
	mediaplayer.on('error', function() {
        console.warn(self.mediaTypeString, 'error - setting complete', mediaplayer.error())
    });
    
    if (vtt){
        self.$component.find('a.accordion-button').on('click', function(){
            // set component complete
            self.componentController.setComponentComplete(self);
        });

        // self.$component.find('.btn.continueBtn').on('click', function(){
            
        //     self.videoComplete();
        // });
    }
    if (self.dataNode.fallback){
        self.$component.find('.fallbackMsg a').on('click', function(){
        
            // set component complete
            self.componentController.setComponentComplete(self);
        });
    }

    // audio muted?
    $(document).on(courseEvent.AUDIO_OFF, function(){
        mediaplayer.muted(true);
    });
    $(document).on(courseEvent.AUDIO_ON, function(){
        mediaplayer.muted(false);
    });
}

/**
 * @public
 * @override 
 * @description  This function is called on unload
 */
ComponentMedia.prototype.unload = function(){
    var self = this;
    if (self.isVideoInitialised){
        console.log(self.mediaTypeString, 'unload');
        var mediaplayer = videojs(this.id+'-'+(self.isVideo ? 'video' : 'audio'));
        mediaplayer.dispose();
    }
}

/**
 * @public
 * @override 
 * @description  This function is to play the video
 */
 ComponentMedia.prototype.play = function(){
    var self = this;
    if (self.isVideoInitialised){
        console.log(self.mediaTypeString, 'play');
        var mediaplayer = videojs(this.id+'-'+(self.isVideo ? 'video' : 'audio'));
        mediaplayer.play();
    }
}

/**
 * @public
 * @override 
 * @description  This function is called on pause
 */
ComponentMedia.prototype.pause = function(){
    var self = this;
    if (self.isVideoInitialised){
        console.log(self.mediaTypeString, 'pause');
        var mediaplayer = videojs(this.id+'-'+(self.isVideo ? 'video' : 'audio'));
        mediaplayer.pause();
    }
}

/**
 * @public
 * @override 
 * @description  This function is called on stop
 */
 ComponentMedia.prototype.stop = function(){
    var self = this;
    if (self.isVideoInitialised){
        console.log(self.mediaTypeString, 'stop');
        var mediaplayer = videojs(this.id+'-'+(self.isVideo ? 'video' : 'audio'));
        mediaplayer.pause();
        // mediaplayer.seek(0);
    }
}

/**
 * @public
 * @override 
 * @description  This function is called on videoComplete
 */
ComponentMedia.prototype.videoComplete = function(){
    var self = this;

    // set component complete
    self.componentController.setComponentComplete(self);

    if (self.nextSlideOnComplete){
        contentController.scrollSlideIntoView(self.$component.closest('.slide').next().index());
    } else if (self.nextOnComplete){
        navigationController.nextPage();
    } 
}