/**
 * @fileoverview This is used to create a Header component.
 * @class
 * @augments Component
 * @author LIVELOGIC
 * @version 1.0
 */
function ComponentHeader(){
}

ComponentHeader.prototype = Object.create(Component.prototype);

/**
 * @function
 * @description  This function initialiases the component
 */
ComponentHeader.prototype.init = function(_componentController, node, _id, _num){
    Component.prototype.init.call(this, _componentController, node, _id, _num);

    var self = this;

    //self.managesCompletion = false;
}

/**
 * @function
 * @description  This function adds the component HTML
 */
ComponentHeader.prototype.getHTML = function(){
    var self = this;
    
    var htmlStr = `
    <div class="header-container">
        <div class="row flex-row">
            <div class="col-sm-6 col-sm-offset-6">
                <div class="header-heading-container">
                    ${htmlUtils.getHTMLText(self.dataNode)}
                </div>
            </div>
        </div>
        <button class="btn header-btn" aria-label="${courseData.getAccessibleLabel('scrollToContentView')}">${svgUtils.createSVG('arrow-down')}</button>
    </div>`
     
    
    return htmlStr;
}

/**
 * @function
 * @description  This function sets up the component
 */
ComponentHeader.prototype.setupComponent = function(){
    Component.prototype.setupComponent.call(this);
    
    var self = this;   
}

/**
 * @function
 * @description  This function fires when sets up complete
 */
ComponentHeader.prototype.setupComplete = function(){
    Component.prototype.setupComplete.call(this);
        
    var self = this;   

    // self.$component.find('.header-btn').click(function(){
    //     navigationController.scrollIntoView($(this).closest('.slide').next());
    // });
    self.$component.find('.header-btn').on('click keypress', utils.handleClickAndPress(function(e){
        var $this = $(e.currentTarget);
        navigationController.scrollIntoView($this.closest('.slide').next(), 'start');    
    }));

    
}

/**
 * @public
 * @override 
 * @description  This function is called on unload
 */
ComponentHeader.prototype.unload = function(){
    var self = this;    
}

/**
 * @public
 * @override 
 * @description  This function fired when component is displayed
 */
ComponentHeader.prototype.componentDisplayed = function(){
    Component.prototype.componentDisplayed.call(this);

    var self = this;    

    
    
    // set component complete
    //self.componentController.setComponentComplete(self);
}
/**
 * @public
 * @override 
 * @description  This function is called on browser resize
 */
ComponentHeader.prototype.resize = function(){
    var self = this;   
    
    if (self.splitText){
        self.splitText.revert();
    }
    self.splitText = new SplitText(self.$component.find('.header-heading >span'), {type: 'lines', tag: 'span', linesClass: 'st-line'});
}

/**
 * @public
 * @override 
 * @description  This function is called on device breakpoint changed
 */
 ComponentHeader.prototype.breakpointChanged = function(){
    var self = this;
 }
