/**
 * @fileoverview This is used to create a Button component.
 * @class
 * @augments Component
 * @author LIVELOGIC
 * @version 1.0
 */
function ComponentTab(){
}

ComponentTab.prototype = Object.create(Component.prototype);

/**
 * @function
 * @description  This function adds the component HTML
 */
ComponentTab.prototype.init = function(_componentController, node, _id, _num){
    Component.prototype.init.call(this, _componentController, node, _id, _num);

    this.managesCompletion = true;
}

/**
 * @function
 * @description  This function adds the component HTML
 */
ComponentTab.prototype.getHTML = function(){
//    console.log('ComponentButton.prototype.getHTML', this.dataNode.text[0].Text);
//    var htmlStr = '<div class="btnText">';
//    if (courseConfig.dataFormat == 'json'){
//        htmlStr += this.dataNode.content.text;
//    } else {
//        htmlStr += this.dataNode.text[0].Text
//    }
//    htmlStr += '</div>';
    var isXML = courseConfig.dataFormat == 'xml';
    var headerHtmlStr = '';
    var htmlStr = '';
    var items = this.dataNode.item;
    //htmlStr += '<div id="'+this.id+'-tab" class="tab-group" role="tablist" aria-multiselectable="true">';
    for (var i = 0; i < items.length; i++){  
        var title = htmlUtils.getHTMLText(items[i].title[0]);
        var text = this.componentController.generateComponents(items[i].reveal[0]);
        var tabImage;
        if (items[i].svg){
            tabImage = svgUtils.createSVG(items[i].svg);
        } else if (items[i].src){
            var path = utils.getImagePath(items[i].src);
            tabImage = '<img src="'+path+'" class="img-responsive"';
            
            if (items[i].alt){
                tabImage += ' alt="'+items[i].alt[0].Text+'"';
            }
            tabImage += '/>';            
        }
        var classStr = '';
        var expanded = 'true';
        if (i > 0){
            expanded = 'false';
            classStr = ' ';//'moduleBtnInvert';
        } else if (i == 0){
            classStr = ' active done';
        }
    headerHtmlStr += '<div class="panel tabWrap hidden-xs'+(tabImage ? '' : ' noicon')+'">';
        headerHtmlStr += '<div id="'+this.id+'-heading'+i+'" class="" role="tab">';
            headerHtmlStr += '<a class="tab-button moduleBtn'+classStr+'" role="button" data-toggle="collapse" data-index="'+i+'" data-parent="#'+this.id+'-tab" href="#'+this.id+'-collapse'+i+'" aria-expanded="'+expanded+'" aria-controls="'+this.id+'-collapse'+i+'">';
                headerHtmlStr += '<div class="tab-icon">'+tabImage+'</div>';
                headerHtmlStr += '<div class="tab-heading">';
                    headerHtmlStr += title
                headerHtmlStr += '</div>';
            headerHtmlStr += '</a>';
        headerHtmlStr += '</div>';
    headerHtmlStr += '</div>';
    htmlStr += '<div class="panel tabAccordionWrap'+(tabImage ? '' : ' noicon')+'">';
        htmlStr += '<div id="'+this.id+'-heading'+i+'" class="visible-xs" role="tab">';
            htmlStr += '<a class="tab-button moduleBtn'+classStr+'" role="button" data-toggle="collapse" data-index="'+i+'" data-parent="#'+this.id+'-tab" href="#'+this.id+'-collapse'+i+'" aria-expanded="'+expanded+'" aria-controls="'+this.id+'-collapse'+i+'">'
                htmlStr += '<div class="tab-icon">'+tabImage+'</div>';
                htmlStr += '<div class="tab-heading">';
                    htmlStr += title
                htmlStr += '</div>';
            htmlStr += '</a>';
        htmlStr += '</div>';
        var collapsedClass=' collapse';
        if (i == 0){
            collapsedClass=' in';
        }
        htmlStr += '<div id="'+this.id+'-collapse'+i+'" class="panel-collapse lightGreyBkg'+collapsedClass+'" role="tabpanel" aria-labelledby="'+this.id+'-heading'+i+'">';
            htmlStr += '<div class="panel-body tab-reveal">'+text+'</div>';
        htmlStr += '</div>';
    htmlStr += '</div>';
    }
    //htmlStr += '</div>';
    htmlStr = '<div id="'+this.id+'-tab" class="tab-group" role="tablist" aria-multiselectable="true">'+headerHtmlStr+htmlStr+'</div>';
    
    return htmlStr;
}

