/**
 * @function
 * @description  This function sets up the component
 */
 ComponentTab.prototype.setupComplete = function(){
    Component.prototype.setupComplete.call(this);
    
    var self = this;

    var $options = self.$component.find('.tab-button');
    var tabOptions = $options.slice(0, $options.length / 2).toArray();

    var nrOptions = $options.length / 2;

    self.$component.on('keydown', function(event){
        var activeIdx = 0;
        for(var i = 0; i < $options.length; i++){
            if($options[i] == document.activeElement){
                activeIdx = i;
                break;
            }
        }
        var nextActiveIdx;
        switch(event.key) {
            case "ArrowLeft":
                if(activeIdx === 0){
                    nextActiveIdx = nrOptions - 1;
                } else {
                    nextActiveIdx = activeIdx - 1;
                }
                tabOptions[nextActiveIdx].focus();
                break;
            case "ArrowRight":
                if(activeIdx === nrOptions - 1){
                    nextActiveIdx = 0;
                } else {
                    nextActiveIdx = activeIdx + 1; 
                }
                tabOptions[nextActiveIdx].focus();
                break;
            default: 
                break;
        }
    });
    
    //$reveals.first().collapse('show');
    
    $options.on('click', function(e){
        var $this = $(this);
        if ($this.hasClass('active')){
            e.preventDefault();
            return false;
        }
        $options.removeClass('active');
        // find corresponding button in tab/accordion
        var $bothOptions = $options.filter(function(){
            return ($(this).data('index') == $this.data('index'))
        })
        $bothOptions.addClass('done active');
        //$(this).removeClass('moduleBtnInvert');
        //$options.not($(this)).enable().addClass('moduleBtnInvert');
        
        
        //$reveals.collapse('hide');
        if ($(this).hasClass('collapsed')){
//            svgUtils.animateSVG('plusminus', $(this));
            // hide any others tht are open
            //self.$component.find('.tab-reveal').parent().not($(this).attr('href')).collapse('hide');
        } else {
//            svgUtils.animateSVG('plusminus-reverse', $(this));
            //self.$component.find('.collapse').not($(this).attr('href')).collapse('show');
        }

        

        //self.childComponents.each
        
        //self.componentController.self.$component.find('.component').componentDisplayed();
        
        var $collapse = self.$component.find($(this).attr('href'));
        setTimeout(function(){
            self.componentController.showChildComponents($collapse);
        },100);

        setTimeout(function(){
            navigationController.checkForScroll();
        },1000)
        
        //console.log($options.filter('.done').length, $options.length);
        if ($options.filter('.done').length === $options.length){
            // set component complete
            self.componentController.setComponentComplete(self);
        }
    }); 
 }

/**
 * @public
 * @override 
 * @description  This function fired when component is displayed
 */
ComponentTab.prototype.componentDisplayed = function(){
    Component.prototype.componentDisplayed.call(this);

    
    
}

/**
 * @public
 * @override 
 * @description  This function fired on resize
 */
ComponentTab.prototype.resize = function(){
    var self = this;
    
    var $tabHeadings = self.$component.find('.tabWrap .tab-heading');
    
    var maxHeight = 0;
    $tabHeadings.height('auto');
    $tabHeadings.each(function(){
        maxHeight = Math.max(maxHeight, $(this).height());
    });
    $tabHeadings.height(maxHeight);

}
