/**
 * @fileoverview This is used to create a StoryCarousel component.
 * @class
 * @augments Component
 * @author LIVELOGIC
 * @version 1.0
 */
function ComponentCarousel(){  
    
}

ComponentCarousel.prototype = Object.create(Component.prototype);

/**
 * @function
 * @description  This function initialiases the component
 */
ComponentCarousel.prototype.init = function(_componentController, node, _id, _num){
    Component.prototype.init.call(this, _componentController, node, _id, _num);

    this.managesCompletion = true;

    this.slidesCompleteArray = [];
}

/**
 * @function
 * @description  This function sets up the component
 */
ComponentCarousel.prototype.setupComponent = function(){
    Component.prototype.setupComponent.call(this);
}

/**
 * @function
 * @description  This function adds the component HTML
 */
ComponentCarousel.prototype.getHTML = function(){
    var self = this;    
    
    var htmlStr = '';
    htmlStr += '<div class="carousel-wrapper" aria-roledescription="carousel" aria-label="'+(courseData.getAriaProperty(self.dataNode, 'label') || courseData.getAccessibleLabel('carousel'))+'">';
        htmlStr += '<div class="bxslider">';
        //console.log(this.dataNode.slide.length);
        for (var i = 0 ; i < this.dataNode.slide.length; i++){
            self.slidesCompleteArray[i] = 0;
            htmlStr += '<div>';
                htmlStr += '<div class="carousel-slide">';
                    htmlStr += this.componentController.generateComponents(this.dataNode.slide[i]);
                htmlStr += '</div>';
            htmlStr += '</div>';
        }
        htmlStr += '</div>';
    htmlStr += '</div>';
    
    return htmlStr;
}

/**
 * @function
 * @description  This function sets up the component
 */
ComponentCarousel.prototype.setupComplete = function(){
    Component.prototype.setupComplete.call(this);
    
    var self = this;  

    var options = {
        infiniteLoop: self.dataNode.infiniteLoop ? self.dataNode.infiniteLoop : false,
        mode: self.dataNode.mode ? self.dataNode.mode : 'horizontal',
        speed: self.dataNode.speed ? self.dataNode.speed : 500,
        adaptiveHeight: self.dataNode.adaptiveHeight ? (self.dataNode.adaptiveHeight == "true") : false,
        controls: self.dataNode.controls ? (self.dataNode.controls == "true") : true,
        pager: self.dataNode.pager ? (self.dataNode.pager == "true") : true,
        touchEnabled: self.dataNode.touchEnabled ? (self.dataNode.touchEnabled == "true") : true,
        hideControlOnEnd: self.dataNode.hideControlOnEnd ? (self.dataNode.hideControlOnEnd == "true") : true,
        nextText: '<span>'+courseData.getBtnText('CarouselNext')+'</span><div class="svg-icon">'+svgUtils.createSVG('chevron-right')+'</div>',
        prevText: '<span>'+courseData.getBtnText('CarouselPrev')+'</span><div class="svg-icon">'+svgUtils.createSVG('chevron-left')+'</div>',
        onSliderLoad: function(){
            // set first carousel slide as complete
            self.slidesCompleteArray[0] = 1;
            // disable pager dot buttons
            setTimeout(function(){
                self.$component.find('.bx-controls .bx-pager-item:gt(1) a').disable();
                // add aria label to pager dot button
                self.$component.find('.bx-controls .bx-prev').attr('tabindex', '0').attr('role', 'button');
                self.$component.find('.bx-controls .bx-next').attr('tabindex', '0').attr('role', 'button');

                // add aria current to pager dot buttons
                self.$component.find('.bx-controls .bx-pager-item a').eq(0).attr('aria-current','true');
                // add aria role to pager dot buttons and set href to javascript:void(0) to avoid page jump
                self.$component.find('.bx-controls .bx-pager .bx-pager-link').attr('tabindex', '0').attr('href', 'javascript:void(0)').attr('role', 'button');
                // adjust text of pager dot buttons
                self.$component.find('.bx-controls .bx-pager .bx-pager-link').each(function(){
                    $(this).html(courseData.getAccessibleLabel('carouselGoToSlide').split('{num}').join($(this).text()));
                });

            }, 500);
            //self.$component.find('.bx-controls .bx-next').addClass('pulseScale');
        },
        onSlideAfter: function($slideElement, oldIndex, newIndex){
            // set this carousel slide as complete
            self.slidesCompleteArray[newIndex] = 1;
            // enable pager dot buttons for next slide too
            self.$component.find('.bx-controls .bx-pager-item a').slice(0, newIndex+1).enable();
            // add aria current to pager dot button
            self.$component.find('.bx-controls .bx-pager-item a').attr('aria-current','false').eq(newIndex).attr('aria-current','true');
            
//            if (self.slidesCompleteArray.indexOf(0) == -1){
            if (newIndex == self.$slider.getSlideCount()-1){
                // last slide lets set the focus as button disappears
                self.$component.find('.bx-prev').focus();

                //console.log('CAROUSEL', newIndex, self.$slider.getSlideCount()-1)
                //self.$component.find('.bx-controls .bx-next').removeClass('pulseScale');
                // set component complete
                self.componentController.setComponentComplete(self);
            } else if (newIndex == 0){
                // first slide lets set the focus as button disappears
                self.$component.find('.bx-next').focus();
            }
        }

    }

    self.$component.addClass(options.mode);

    self.$slider = self.$component.find('.bxslider').bxSlider(options);
}
/**
 * @public
 * @override 
 * @description  This function is called on unload
 */
ComponentCarousel.prototype.unload = function(){
    var self = this;    
}

/**
 * @public
 * @override 
 * @description  This function fired when component is displayed
 */
ComponentCarousel.prototype.componentDisplayed = function(){
    Component.prototype.componentDisplayed.call(this);
    console.log('ComponentCarousel::componentDisplayed');

    var self = this;    
    
    self.$slider.redrawSlider();
    
}
/**
 * @public
 * @override 
 * @description  This function is called on browser resize
 */
ComponentCarousel.prototype.resize = function(){
    var self = this;    
}
