/**
 * @fileoverview This is used to create a Highchart_Bar component.
 * @class
 * @augments Component
 * @author LIVELOGIC
 * @version 1.0
 */
function ComponentHighchart_Bar(){
}

ComponentHighchart_Bar.prototype = Object.create(Component.prototype);

/**
 * @function
 * @description  This function initialiases the component
 */
ComponentHighchart_Bar.prototype.init = function(_componentController, node, _id, _num){
    Component.prototype.init.call(this, _componentController, node, _id, _num);

    var self = this;

    //self.managesCompletion = false;

    self.colors = ['#5d9438', '#f14e73', '#ed6335', '#3d8eae', '#943888', '#edb735', '#ba1110'];

    Highcharts.setOptions({
        lang: {
            numericSymbols: null,
            thousandsSep: ','
        }
    });
}

/**
 * @function
 * @description  This function adds the component HTML
 */
ComponentHighchart_Bar.prototype.getHTML = function(){
    var self = this;   
    
    var htmlStr = '';
    
    htmlStr += '<figure class="highcharts-figure">';// role="application">';
        htmlStr += '<div id="'+self.componentID+'-bar-chart"></div>';
    htmlStr += '</figure>';
            
    
    return htmlStr;
}

/**
 * @function
 * @description  This function sets up the component
 */
ComponentHighchart_Bar.prototype.setupComponent = function(){
    Component.prototype.setupComponent.call(this);
    
    var self = this;


    self.chartOptions = {
       
         colors: self.colors,
        accessibility: {
            keyboardNavigation: {
                focusBorder: {
                    margin: 4,
                    style: {
                        borderWidth: 2,
                        borderRadius: 0,
                        color: '#DB0011' // hsbcred
                    }
                }
            }
        //     point: {
        //         descriptionFormatter: function (p) {
        //             return p.series.name + ', ' + p.category + ', ' + p.y + 'F.';
        //         }
        //     }
        },
        title: {
            text: self.dataNode.title[0].Text
        },
        xAxis: {
            crosshair: true
        },
        yAxis: {
            min: 0,
            title: {
                text: self.dataNode.yAxis[0].title[0].Text
            },
            tickInterval: 2000,
            labels: {
                formatter: function() {
                    return Highcharts.numberFormat(this.value, null, null, ',');
                }
            }
        },
        plotOptions: {
            column: {
                borderWidth: 0
            }
        }
    };

    // chart type
    if (self.dataNode.chartType){
        self.chartOptions.chart = {
            type: self.dataNode.chartType
        }
    }
    // description
    if (self.dataNode.description){
        self.chartOptions.accessibility = {
            description: self.dataNode.description[0].Text
        }
    }

    // series options
    self.seriesData = [];
    for (var i = 0; i < self.dataNode.data.length; i++) {
        self.seriesData[i] = {
            name: self.dataNode.data[i].name[0].Text, 
            data: []
        };
        // type
        if (self.dataNode.data[i].type){
            self.seriesData[i].type = self.dataNode.data[i].type;
        }
        // series color options
        if (self.dataNode.data[i].color){
            self.seriesData[i].color = self.dataNode.data[i].color;
        }
        // series marker options
        if (self.dataNode.data[i].marker){
            self.seriesData[i].marker = {};
            if (self.dataNode.data[i].marker[0].symbol){
                self.seriesData[i].marker.symbol = self.dataNode.data[i].marker[0].symbol;
            }
            if (self.dataNode.data[i].marker[0].lineWidth){
                self.seriesData[i].marker.lineWidth = parseInt(self.dataNode.data[i].marker[0].lineWidth);
            }
            if (self.dataNode.data[i].marker[0].lineColor){
                self.seriesData[i].marker.lineColor = self.dataNode.data[i].marker[0].lineColor;
            }
            if (self.dataNode.data[i].marker[0].fillColor){
                self.seriesData[i].marker.fillColor = self.dataNode.data[i].marker[0].fillColor;
            }
        }
        // series tooltip options
        if (self.dataNode.data[i].tooltip){            
            self.seriesData[i].tooltip = {};
            if (self.dataNode.data[i].tooltip[0].valuePrefix){
                self.seriesData[i].tooltip.valuePrefix = self.dataNode.data[i].tooltip[0].valuePrefix[0].Text;
            }
            if (self.dataNode.data[i].tooltip[0].valueSuffix){
                self.seriesData[i].tooltip.valueSuffix = self.dataNode.data[i].tooltip[0].valueSuffix[0].Text;
            }
        }
        for (var j = 0; j < self.dataNode.data[i].item.length; j++) {
            self.seriesData[i].data.push(parseInt(self.dataNode.data[i].item[j].val));
        }
    }
    self.chartOptions.series = self.seriesData;

    // x axis options
    if (self.dataNode.xAxis){
        if (self.dataNode.xAxis[0].title){
            self.chartOptions.xAxis.title = {
                text: self.dataNode.xAxis[0].title
            }
        }
        if (self.dataNode.xAxis[0].description){
            self.chartOptions.xAxis.accessibility = {
                description: self.dataNode.xAxis[0].description[0].Text
            }
        }
    }
    self.xAxisCategories = [];
    for (var i = 0; i < self.dataNode.xAxis[0].category.length; i++) {
        self.xAxisCategories[i] = self.dataNode.xAxis[0].category[i].Text;
    }
    self.chartOptions.xAxis.categories = self.xAxisCategories;

    // y axis options
    if (self.dataNode.yAxis){
        if (self.dataNode.yAxis[0].description){
            self.chartOptions.yAxis.accessibility = {
                description: self.dataNode.yAxis[0].description[0].Text
            }
        }
    }

    // tooltip options
    if (self.dataNode.tooltip){
        self.chartOptions.tooltip = {};
        if (self.dataNode.tooltip[0].valuePrefix){
            self.chartOptions.tooltip.valuePrefix = self.dataNode.tooltip[0].valuePrefix[0].Text;
        }
        if (self.dataNode.tooltip[0].valueSuffix){
            self.chartOptions.tooltip.valueSuffix = self.dataNode.tooltip[0].valueSuffix[0].Text;
        }
    }
}

/**
 * @function
 * @description  This function fires when sets up complete
 */
ComponentHighchart_Bar.prototype.setupComplete = function(){
    Component.prototype.setupComplete.call(this);
    
    var self = this;   
}

/**
 * @public
 * @override 
 * @description  This function is called on unload
 */
ComponentHighchart_Bar.prototype.unload = function(){
    var self = this;
    
    if (self.chart){
        self.chart.destroy();
    }
}

/**
 * @public
 * @override 
 * @description  This function fired when component is displayed
 */
ComponentHighchart_Bar.prototype.componentDisplayed = function(){
    Component.prototype.componentDisplayed.call(this);

    var self = this;    
    
    // set component complete
    //self.componentController.setComponentComplete(self);

    // Bar chart
    self.chart = Highcharts.chart(self.componentID+'-bar-chart', self.chartOptions);

    if (self.chart){
        self.chart.reflow();
    }
}
/**
 * @public
 * @override 
 * @description  This function is called on browser resize
 */
ComponentHighchart_Bar.prototype.resize = function(){
    var self = this;    
}

/**
 * @public
 * @override 
 * @description  This function is called on device breakpoint changed
 */
 ComponentHighchart_Bar.prototype.breakpointChanged = function(){
    var self = this;
 }
