/**
 * @fileoverview This is used to create an Image component.
 * @class
 * @augments Component
 * @author LIVELOGIC
 * @version 1.0
 */
function ComponentImage(){
}

ComponentImage.prototype = Object.create(Component.prototype);

/**
 * @function
 * @description  This function adds the component HTML
 */
ComponentImage.prototype.getHTML = function(){
    var self = this;
    var svg = this.dataNode.svg;
        
    var htmlStr = '';
    if (svg){
        htmlStr += svgUtils.createSVG(svg);
    } else{
        var path = utils.getImagePath(this.dataNode.src);

        if(this.dataNode.mob_src) {
            var mob_path = utils.getImagePath(this.dataNode.mob_src);
        }
        else{
            var mob_path = null;
        }

        var altText = (this.dataNode.alt) ? this.dataNode.alt[0].Text : '';
        altText = altText || '';

        //htmlStr += '<div class="aligned-image" style="background-image: url('+path+'); '+htmlUtils.getStyles(self.dataNode)+'"></div>';
       if(mob_path == null) {
        htmlStr += '<img src="'+path+'" class="img-responsive" alt="'+altText+'"/>';
       }
       else{
        htmlStr += '<img src="'+path+'" class="img-responsive hidden-xs" alt="'+altText+'"/>';
        htmlStr += '<img src="'+mob_path+'" class="img-responsive visible-xs" alt="'+altText+'"/>';
       }
    }
    
    if (this.dataNode.label){
        htmlStr += '<div class="img-label">'+htmlUtils.getHTMLText(this.dataNode.label[0])+'</div>';
    }
    if (this.dataNode.hotspots){
        htmlStr += '<div class="hotspots">';
            htmlStr += this.componentController.generateComponents(this.dataNode.hotspots[0]);
    //+this.dataNode.label[0].Text+
        htmlStr += '</div>';
    }
    
    return htmlStr;
} 

/**
 * @function
 * @description  This function sets up the component
 */
ComponentImage.prototype.setupComponent = function(){
    Component.prototype.setupComponent.call(this);
    //console.log('ComponentImage.prototype.setupComponent', this.id);
    var svg = this.dataNode.svg;
    var self = this;
    if (svg){
        self.timeline = svgUtils.animateSVG(svg, this.$component);
        self.timeline.pause();
        this.$component.on('inview_anim_complete', function(){
            
            self.timeline.restart();
            self.timeline.play();
        });
    }
    // apply image to component background in case we're using a height aligned row - display controlled by CSS
    self.$component.css('background-image', 'url('+self.$component.find('img').attr('src')+')');

}

/**
 * @public
 * @override 
 * @description  This function fired when component is displayed
 */
ComponentImage.prototype.componentDisplayed = function(){
    Component.prototype.componentDisplayed.call(this);

    //console.log('ComponentImage:componentDisplayed');
    var self = this;
    
    if (self.timeline){
        //has an SVG timeline
        if (self.$component.hasClass('animated')){
            self.timeline.restart();
            self.timeline.play();            
        }
    }
}

/**
 * @function
 * @description  This function sets up the component
 */
ComponentImage.prototype.setupComplete = function(){
    Component.prototype.setupComplete.call(this);
    
    var self = this;

    
}

/**
 * @public
 * @override 
 * @description  This function is called on unload
 */
ComponentImage.prototype.unload = function(){
    var self = this;
}