/**
 * @fileoverview This is used to create a Text component.
 * @class
 * @augments Component
 * @author LIVELOGIC
 * @version 1.0
 */
function ComponentText(){
}

ComponentText.prototype = Object.create(Component.prototype);

/**
 * @function
 * @description  This function adds the component HTML
 */
ComponentText.prototype.init = function(_componentController, node, _id, _num){
    Component.prototype.init.call(this, _componentController, node, _id, _num);
   
    var self = this;

   

    self.textanim = (self.dataNode.textanim == "true");
    self.textanimTimeline;

    //if (self.textanim){
       self.managesCompletion = true;
    // } else {
    //     self.managesCompletion = false;
    // }
}

/**
 * @override 
 * @description  This function adds the component HTML
 */
ComponentText.prototype.getHTML = function(){
    var self = this;
    var htmlStr = '';
    if (courseConfig.dataFormat == 'xml') {
        htmlStr += htmlUtils.getHTMLText(this.dataNode);
    } else {
        htmlStr += this.dataNode.content.text; 
    } 
    
    //htmlStr += this.componentController.generateComponents(this.dataNode);
    //this.dataNode.content[0].text : this.dataNode.content.text;//this.dataNode.content.text;//'<textarea class="form-control"></textarea>';//componentController.getHTMLText(dataNode);
    return htmlStr;
} 

/**
 * @public
 * @override 
 * @description  This function fired when component is displayed
 */
ComponentText.prototype.componentDisplayed = function(){
    Component.prototype.componentDisplayed.call(this);

    var self = this;

    if (self.textanim){

        self.$component.on('inview_anim_complete', function(){
            // console.log('inview_anim_complete');
            self.textanimTimeline.play();
        });

        // self.$component.find('.anim').last().on('inview_anim_complete', function(){
        //     // set component complete
        //     self.componentController.setComponentComplete(self);
        // });
    } else {
        var $anims = htmlUtils.findAnims(self.$component, ':visible');//self.$component.find('.anim').filter(':visible');
        if ($anims.length){
            $anims.last().on('inview_anim_complete', function(){
                // console.log('ComponentText inview_anim_complete', self.id)
                // set component complete
                self.componentController.setComponentComplete(self);
            });
        } else {
            // set component complete
            self.componentController.setComponentComplete(self);
        }
    }   
}

/**
 * @public
 * @override 
 * @description  This function fired when component is displayed
 */
ComponentText.prototype.setupComplete = function(){
    Component.prototype.setupComplete.call(this);
    
    var self = this;

    if (self.textanim){ 
        var $typeEl = self.$component;//.find('.email-body');
        //console.log($typeEl);
        var mySplitText;
        self.textanimTimeline = new gsap.timeline({
            onComplete: function(){
                mySplitText.revert();
                // set component complete
                self.componentController.setComponentComplete(self);
            }
        });//TimelineLite();
        
        $typeEl.children().each(function(){
            //console.log($(this)[0].nodeName);
            if ($(this)[0].nodeName.toUpperCase() == 'H4'){
                self.textanimTimeline.from($(this), .5, {opacity: 0, y: 10});
            } else {
                // check if RTL language - gsap SplitText not compatible with RTL
                if (languageConfig.languages[currentLanguage].direction != 'rtl'){
                    mySplitText = new SplitText($(this), {type:"words,chars"});
                    self.textanimTimeline.staggerFrom(mySplitText.chars, .5, {opacity: 0, y: 10}, 0.05);
                } else {                    
                    mySplitText = new SplitText($(this), {type:"words"});
                    self.textanimTimeline.staggerFrom(mySplitText.words, .5, {opacity: 0, y: 10}, 0.1);
                }
            }
        });

            //mySplitText = new SplitText($typeEl, {type:"words,chars"});

        // console.log(self.textanimTimeline.duration());
        self.textanimTimeline.pause();
    }
}


/**
 * @public
 * @override 
 * @description  This function is called on browser resize
 */
ComponentText.prototype.resize = function(){
    var self = this;    
    
    if (self.splitText){
        self.splitText.revert();
    }
    self.splitText = new SplitText(self.$component.find('.heading-with-bg > span'), {type: 'lines', tag: 'span', linesClass: 'st-line'});
}