/**
 * @fileoverview This is used to create a Button component.
 * @class
 * @augments Component
 * @author LIVELOGIC
 * @version 1.0
 */
function ComponentButton(){
}

ComponentButton.prototype = Object.create(Component.prototype);

/**
 * @function
 * @description  This function initialiases the component
 */
ComponentButton.prototype.init = function(_componentController, node, _id, _num){
    Component.prototype.init.call(this, _componentController, node, _id, _num);

    var self = this;

    //self.managesCompletion = false;
}

/**
 * @function
 * @description  This function adds the component HTML
 */
ComponentButton.prototype.getHTML = function(){
//    console.log('ComponentButton.prototype.getHTML', this.dataNode.text[0].Text);
    var htmlStr = '';
    if (this.dataNode.src){
        var path = utils.getImagePath(this.dataNode.src);
        var altText = (this.dataNode.alt) ? this.dataNode.alt[0].Text : '';
        htmlStr += '<img aria-hidden="true" src="'+path+'" class="btnImage img-responsive" alt="'+altText+'"/>';
    }
    if (this.dataNode.svg){
         htmlStr += '<div class="svgIcon svg-original" aria-hidden="true">'+svgUtils.createSVG(this.dataNode.svg)+'</div>';
    }

    if (this.dataNode.svgComplete){
        htmlStr += '<div class="svgIcon svg-complete" aria-hidden="true">'+svgUtils.createSVG(this.dataNode.svgComplete)+'</div>';
    }
    
    if (this.dataNode.submenu === "true"){
        htmlStr += '<div class="status">'+svgUtils.createSVG('tick')+'</div>';
    }
    
    if (this.dataNode.text){
    htmlStr += '<div class="btnText">';
        htmlStr += htmlUtils.getHTMLText(this.dataNode.text[0]);
    htmlStr += '</div>';
    }
    if (this.dataNode.subtext){
    htmlStr += '<div class="btnSubtext">';
        htmlStr += htmlUtils.getHTMLText(this.dataNode.subtext[0]);
    htmlStr += '</div>';
    }
    return htmlStr;
}

/**
 * @function
 * @description  This function sets up the component
 */
ComponentButton.prototype.setupComponent = function(){
    Component.prototype.setupComponent.call(this);
    
    var self = this;
    
    this.$component.addClass('btn');
    if (this.dataNode.src){
        this.$component.addClass('btn-image');
    }
    if (this.dataNode.svg){
        this.$component.addClass('btn-svg');
    }
    if (this.dataNode.submenu === "true"){
        this.$component.addClass('btn-submenu');
    }
    if (!this.dataNode.text){
        this.$component.addClass('btn-nolabel');
    }

    // check if a hotspot and add sr-only class to button text
    if (this.$component.hasClass('hotspot')){
        if(this.dataNode.displayText){

        }
        else{
             this.$component.find('.btnText').addClass('sr-only');
        }
    }

    // add accessibility attributes
    self.$component
        .attr('role', 'button')
        .attr('tabindex', '0');      
}

/**
 * @function
 * @description  This function sets up the component
 */
ComponentButton.prototype.setupComplete = function(){
    Component.prototype.setupComplete.call(this);
    
    var self = this;

    if (self.$component.hasClass('backBtn')){
        self.$component.on('click', function(){
            if (!navigationConfig.linearProgression){
                if (navigationController.moduleIndex <= courseData.getMenuModuleNum()){
                    // if (navigationController.pageIndex > 0){
                    //     navigationController.changePageToData(navigationController.moduleIndex, navigationController.pageIndex-1, 0);
                    // } else {
                    //     navigationController.changePageToData(navigationController.moduleIndex-1, navigationController.pageIndex, 0);
                    // }
                    navigationController.gotoPage(navigationController.currentPage-1);
                } else if (navigationController.pageIndex > 0){
                    // goto previous page in history 
                    navigationController.previousPage();
                } else {
                    navigationController.showMenu();
                }
            } else {
                // goto previous page in history 
                navigationController.previousPage();
            }
        });
	}
    if(self.$component.hasClass('hotspot')){
        self.$component.on('click', function(){
            if(!self.$component.hasClass('done')){
                self.$component.find('.btnText').append('<br><span class="sr-only">'+courseData.getAccessibleLabel('complete')+'</span>');
            }
        });
    }
}

/**
 * @public
 * @override 
 * @description  This function is called on unload
 */
ComponentButton.prototype.unload = function(){
    var self = this;    
}

/**
 * @public
 * @override 
 * @description  This function fired when component is displayed
 */
ComponentButton.prototype.componentDisplayed = function(){
    Component.prototype.componentDisplayed.call(this);

    var self = this;    
    
    if (self.$component.hasClass('continueBtn')){
        
    }
    if (self.$component.hasClass('exitBtn') || 
        self.$component.hasClass('backBtn')){
        // set component complete
        self.componentController.setComponentComplete(self);
    }
    
    if (this.dataNode.submenu === "true"){
        console.log(this.dataNode.actions);
        
        for (x in this.dataNode.actions){
            if (x == 'gotopage'){
                // check if complete
                if (storageController.isPageComplete(navigationController.currentPage+parseInt(this.dataNode.actions[x].split('+').join('')))){
                    this.$component.addClass('done');
                    self.componentController.setComponentComplete(self);
                }
            }
        }
    }
}

